import React, {  useContext, useEffect, useState } from 'react'
import { select } from 'lib/selectors'
import { isMobileScreen } from 'lib/helpers'
import { disconnectConversation, showDomElements } from './../Utils/helper'
import Button from './button'
import CloseButton from './closeButton'
import useDevices from '../hooks/useDevices'
import Dropdown from './dropdown'
import { DEFAULT_AUDIO_CONSTRAINTS, DEFAULT_VIDEO_MOBILE_CONSTRAINTS, DEFAULT_VIDEO_DESKTOP_CONSTRAINTS} from '../Utils/constants'
import { VideoContext } from './chanelTwilioVideo'
import usePrevious from './../hooks/usePrevious'
import { addBackground, removeBackground, loadBackground } from '../Utils/video-processors'
import { createLocalTracks, createLocalVideoTrack, createLocalAudioTrack, connect } from 'twilio-video'

const DeviceSelection = (props) => {
  const {heading, mediaInactiveMessage, cameraDropdownLabel, microphoneDropdownLabel, rejoinCta, demandMediaAccessCta, mediaDisclaimer, cameraOffText, cameraOnText, audioOffText, audioOnText, backgroundOffText, backgroundOnText, closeText, cameraDropdownTitleSubText, microphoneDropdownTitleSubText, isBA, roomId } = jsConfigSecond.twilio
  const [buttonDisable, setButtonDisable] = useState(false)
  const [mediaAccessGranted, setMediaAccessGranted] = useState(false)
  const [cameraDropdownOptions, setCameraDropdownOptions] = useState(null)
  const [microphoneDropdownOptions, setMicrophoneDropdownOptions] = useState(null)
  const [isMobileView, setIsMobileView] = useState(isMobileScreen())
  const [isMicEnabled, setIsMicEnabled] = useState(true)
  const [isVideoEnabled, setIsVideoEnabled] = useState(true)
  const [isBackgroundEnabled, setIsBackgroundEnabled] = useState(false)
  const [audioTrack, setAudioTrack] = useState(null)
  const [videoTrack, setVideoTrack] = useState(null)
  const [garbageTrack, setGarbageTrack] = useState(null)
  const defaultVideoConstraints = (isMobileView)?{...DEFAULT_VIDEO_MOBILE_CONSTRAINTS}:{...DEFAULT_VIDEO_DESKTOP_CONSTRAINTS}
  const [selectedVideoDeviceId, setSelectedVideoDeviceId] = useState(null)
  const [selectedAudioDeviceId, setSelectedAudioDeviceId] = useState(null)
  const {setRoom, token }= useContext(VideoContext)
  const prevStateValue = usePrevious({selectedVideoDeviceId, selectedAudioDeviceId})
  const localMediaContainer = select('.localMedia__container')
  const maxSubscriptionBitrateVal  = isMobileScreen ? 2400000 : 0
  const maxAudioBitrateVal  = isMobileScreen ? 16000 : 0

  var devices = useDevices()
 
  const dropdownSelect = (newDevice) => {

    if (newDevice && newDevice.kind === 'videoinput') {
      setSelectedVideoDeviceId(newDevice.deviceId)
    }
    else if (newDevice && newDevice.kind === 'audioinput') {
      setSelectedAudioDeviceId(newDevice.deviceId)
    }
  }

  const localTrackConstraints = () => {
    return {
      video: {
        name: `${isBA ? 'ba' : 'oc'}` + `-camera-${isMobileView ? 'isMobile-' : '' }${Date.now()}`,
        ...defaultVideoConstraints,
        deviceId: selectedVideoDeviceId
      },
      audio: {
        ...DEFAULT_AUDIO_CONSTRAINTS,
        deviceId: selectedAudioDeviceId
      }
    }
  }

  const authorizeAccess = () => {
    setButtonDisable(true)
    createLocalTracks(localTrackConstraints()).then(function (localTracks) {
      setGarbageTrack(localTracks)
      devices.handleChange()
      setButtonDisable(false)
      setMediaAccessGranted(true)
      setValueForDropdownAndDeviceId()
    })
  }

  const updateLocalTrack = () => {
    garbageTrack && garbageTrack.forEach(tracks => tracks.stop())
    if (prevStateValue.selectedVideoDeviceId !== selectedVideoDeviceId) {
      createLocalVideoTrack(localTrackConstraints().video).then(function (localVideoTracks) {
        setVideoTrack(localVideoTracks)
      })
    }
    if (prevStateValue.selectedAudioDeviceId !== selectedAudioDeviceId) {
      createLocalAudioTrack(localTrackConstraints().audio).then(function (localAudioTracks) {
        !isMicEnabled ? localAudioTracks.disable() : ''
        setAudioTrack(localAudioTracks)
      })
    }
  }

  const addVideoToDom = () => {
    var videoEl = select('video', localMediaContainer )
      videoEl = videoTrack.attach()
      videoEl.muted = true
      videoEl.setAttribute("aria-hidden", true)
      videoEl.setAttribute("tabindex", "-1")
      localMediaContainer.innerHTML = ''
        localMediaContainer.appendChild(videoEl)
  }

  const rejoinAction = () => {
      connect(token, {
        name: roomId,
        tracks: [audioTrack, videoTrack],
        preferredVideoCodecs: [{ codec: "VP8" }],
        bandwidthProfile: {
          video: {
            mode: isBA ? "collaboration" : "presentation",
            dominantSpeakerPriority: isBA ? "high" : "standard",
            ...(!isBA && {maxSubscriptionBitrate: maxSubscriptionBitrateVal})
          },
        },
        networkQuality: {
          local: 1,
          remote: 1,
        },
        ...(!isBA && {maxAudioBitrate: maxAudioBitrateVal})
      })
        .then((twilioRoom) => {
          setRoom(twilioRoom)
          window.Room = twilioRoom
          props.reJoinedClicked(true)
        })
        .catch((error) => {
          console.log("Could not connect to the Room:", error.message)
        })

    garbageTrack && garbageTrack.forEach(tracks => tracks.stop()) && setGarbageTrack(null)
    videoTrack && videoTrack.detach()
    audioTrack && audioTrack.detach()
  }

  const closeModal = () => {
    isBA && disconnectConversation()
    garbageTrack && garbageTrack.forEach(tracks => tracks.stop())
    videoTrack && videoTrack.stop()
    audioTrack && audioTrack.stop()
    props.closeClicked(true)
    showDomElements(true)
    const openModalCTA = select("#check-media-access-button")
    if (openModalCTA) {
      openModalCTA.focus()
    }
  }

  const resizeHandler = () => {
    setIsMobileView(isMobileScreen())
  }

  const videoToggle = () => {

    if (isVideoEnabled) {
      videoTrack.stop()
      localMediaContainer.innerHTML = ''
    } else {
      videoTrack.restart()
      addVideoToDom()
    }
    setIsVideoEnabled(!isVideoEnabled)
  }

  const audioToggle = () => {

    isMicEnabled ? audioTrack.disable() : audioTrack.enable()
    setIsMicEnabled(!isMicEnabled)
  }

  const backgroundToggle = () => {
    !isBackgroundEnabled ? addBackground(videoTrack) : removeBackground(videoTrack)    
    setIsBackgroundEnabled(!isBackgroundEnabled)
  }

  const setValueForDropdownAndDeviceId = () => {
    setCameraDropdownOptions(devices.videoInputDevices)
    setMicrophoneDropdownOptions(devices.audioInputDevices)
    setSelectedAudioDeviceId(devices.audioInputDevices[0].deviceId)
    setSelectedVideoDeviceId(devices.videoInputDevices[0].deviceId)
  }

  useEffect(() => {
    loadBackground()
    select("body").classList.add("has-modal")
    window.addEventListener('resize', resizeHandler)
    window.addEventListener('unload', disconnectConversation)
    window.addEventListener('beforeunload', disconnectConversation)
    return () => {
      window.removeEventListener('resize', resizeHandler)
      window.removeEventListener('unload', disconnectConversation)
      window.removeEventListener('beforeunload', disconnectConversation)
      select("body").classList.remove("has-modal")
    }
  }, [])

  useEffect(() => {
    if(mediaAccessGranted){
      setValueForDropdownAndDeviceId()
    }
  }, [devices.videoInputDevices.length, devices.audioInputDevices.length])
  
  useEffect(() => {
    if(selectedVideoDeviceId || selectedAudioDeviceId){
      updateLocalTrack()
    }
    return () => {
    }
  }, [selectedVideoDeviceId, selectedAudioDeviceId])

  useEffect(() => {
    if(videoTrack ){
      // if the video is disabled via icon and videoInput device is changed from the dropdown, disable the new videotrack
      isBackgroundEnabled ? addBackground(videoTrack) : ''
      isVideoEnabled ? addVideoToDom(): videoTrack.disable()
    }
  }, [videoTrack])

  return (
      <div className="deviceSelection__modal" role="dialog" aria-label={heading} aria-modal="true">
          <div className="deviceSelection__wrapper" role="document">
              <div className="deviceSelection__close">
                  <CloseButton onClick={closeModal} buttonText={`${closeText} - ${heading}`}/>
              </div>
              <div className="deviceSelection__header">
                  <p className="heading is-4" role="heading" aria-level="1">
                      {heading}
                  </p>
              </div>
              <div className="deviceSelection__media-container">
                  <div className="deviceSelection__status" role="status">
                      {!mediaAccessGranted && <p>{mediaInactiveMessage}</p>}
                  </div>
                  <div className="localMedia__container">
                  </div>
                  <div className="localMedia__control-icons">
                      <ul>
                          <li>
                              <Button
                                  onClick={audioToggle}
                                  disabled={!mediaAccessGranted}
                                  spanClass="is-sr-only"
                                  buttonText={isMicEnabled ? audioOffText : audioOnText}
                                  iconName={isMicEnabled ? "microphone-on" : "microphone-off"}
                                  iconSize="large"
                              />
                          </li>
                          <li>
                              <Button
                                  onClick={videoToggle}
                                  disabled={!mediaAccessGranted}
                                  spanClass="is-sr-only"
                                  buttonText={isVideoEnabled ? cameraOffText : cameraOnText}
                                  iconName={isVideoEnabled ? "video-on" : "video-off"}
                                  iconSize="large"
                              />
                          </li>
                      </ul>
                      <ul>
                          <li>
                              <Button
                                  onClick={backgroundToggle}
                                  disabled={!mediaAccessGranted}
                                  spanClass="is-sr-only"
                                  buttonText={isBackgroundEnabled ? backgroundOffText : backgroundOnText}
                                  iconName={isBackgroundEnabled ? "background-on" : "background-off"}
                                  iconSize="large"
                              />
                          </li>
                      </ul>
                  </div>
              </div>
              {!isMobileView && (
                  <div className="deviceSelection__dropdown-container">
                      <Dropdown
                          dropdownIcon="video-on"
                          onSelect={dropdownSelect}
                          dropdownOptions={cameraDropdownOptions}
                          dropdownLabel={cameraDropdownLabel}
                          dropdownTitle={cameraDropdownTitleSubText}
                          disabled={!mediaAccessGranted}
                      />
                      <Dropdown
                          dropdownIcon="microphone-on"
                          onSelect={dropdownSelect}
                          dropdownOptions={microphoneDropdownOptions}
                          dropdownLabel={microphoneDropdownLabel}
                          dropdownTitle={microphoneDropdownTitleSubText}
                          disabled={!mediaAccessGranted}
                      />
                  </div>
              )} 
              <p className={!isMobileView ? "is-sr-only" : "deviceSelection__mobileDisplayText"}>{mediaDisclaimer}</p>
              <div className="deviceSelection__action">
                  <Button
                      onClick={mediaAccessGranted ? rejoinAction : authorizeAccess}
                      buttonDisable={buttonDisable}
                      buttonText={mediaAccessGranted ? rejoinCta : demandMediaAccessCta}
                      buttonClass="is-primary"
                  />
              </div>
          </div>
         
      </div>
  )
}

export default DeviceSelection