import React, { useEffect, useState, useContext, createContext, useRef } from 'react'
import Button from './button'
import { isDesktopScreen, isWideScreen, isMobileScreen, focusElement } from 'lib/helpers'
import { select, getCollection } from 'lib/selectors'
import { VideoContext } from './chanelTwilioVideo'
import CloseButton from './closeButton'
import UserIcon from './userIcon'
import Modal from 'react-modal'
import ProductCatalog from './productCatalog'
import { addBackground, removeBackground } from '../Utils/video-processors'
import { functionalCTADLPush, showDomElements, disconnectConversation, adjustContainerHeight, getFormattedMsgTime, a11yNotificationText, getMyDetails } from '../Utils/helper'
import ShareProductModal from './shareProductModal'
import Chat from './chatBox/chat'
export const ShareProductContext = createContext({ shareProduct: null, setShareProduct: (value) => { }, shareMessageId: '', setShareMessageId: (value) => { }, isShareProductModalOpen: false, setIsShareProductModalOpen: (value) => { }, isProdCatEnabled: false, setIsProdCatEnabled: (value) => { }, isShareProductModalClose: false, setIsShareProductModalClose: (value) => { } })
export const ChatContext = createContext({ messages: null, setMessages: (value) => { } })
import { ecommerceAnalyticsDLPush } from '../Utils/analytics'
import useClients from '../hooks/useClients'
import Icon from "./Icon"
import SwiperCore,{ Navigation } from "swiper"
SwiperCore.use([Navigation])
import { Swiper, SwiperSlide } from "swiper/react"

const ChanelTwilioConsultation = (props) =>
{
  const {
    leavePopinHeadingBA,
    leavePopinTextBA,
    leavePopinBtnConfirmBA,
    leavePopinHeading,
    leavePopinText,
    leavePopinBtnConfirm,
    leavePopinBtnCancel,
    closeText,
    heading,
    consultationText,
    consultationLeave,
    consultationAriaLabel,
    isBA,
    conMicPhOff,
    conMicPhOn,
    conCamOff,
    conCamOn,
    conRevCamOff,
    conRevCamOn,
    conChatOff,
    conChatOn,
    conBgOff,
    conBgOn,
    conSharePrd,
    conMoreFun,
    localVideoAriaLabel,
    remoteVideoAriaLabel,
    isMasterClass,
    addToCartCTA,
    headerText,
    hostText,
    chatMsgAuthorSpanTxt,
    chatProductClientMessage,
    chatProdBAMsgClient,
    productRef,
    availableAct,
    screenShareA11y,
    screenShareStart,
    screenShareStop,
    mcVideoAriaLabel,
    prevLiveView,
    nextLiveView,

  } = jsConfigSecond.twilio

  const prevRef = useRef(null)
  const nextRef = useRef(null)
  const [isMicEnabled, setIsMicEnabled] = useState(true)
  const [isVideoEnabled, setIsVideoEnabled] = useState(true)
  const [isBackgroundEnabled, setIsBackgroundEnabled] = useState(false)
  const { isChatEnabled, setIsChatEnabled, setNotificationModalIsOpen, clientList, userIconText, setUserIconText, waitCount, setWaitCount, setConnectedCount } = useContext(VideoContext)
  const [isReverseCameraEnabled, setIsReverseCameraEnabled] = useState(false)
  const [isDesktop, setIsDesktop] = useState(isWideScreen())
  const { room, conversation } = useContext(VideoContext)
  const [localParticipantDisplayed, setLocalParticipantDisplayed] = useState(false)
  const [isMobile, setIsMobile] = useState(isMobileScreen())
  const [modalIsOpen, setIsOpen] = useState(false)
  const [isProdCatEnabled, setIsProdCatEnabled] = useState(false)
  const [isShareProductModalClose, setIsShareProductModalClose] = useState(false)
  const [totalParticipant, setTotalParticipant] = useState(0)
  const [leaveModalIsOpen, setLeaveModalIsOpen] = useState(false)
  const [shareProduct, setShareProduct] = useState(null)
  const [shareMessageId, setShareMessageId] = useState('')
  const [isFirstTimeOpen, setIsFirstTimeOpen] = useState(true)
  const [isShareProductModalOpen, setIsShareProductModalOpen] = useState(false)
  const hiddenRoomTitle = useRef(null)
  const consultationElm = useRef(null)
  let facingMode
  const [messages, setMessages] = useState(null)
  const chatContainer = select(".chat__container")
  const [screenTrack, setScreenTrack] = useState(null)
  let remoteVideoContainer,localVideoContainer, divEl
  const [totalSlides, setTotalSlides] = useState(0)

  useClients()
  setWaitCount(clientList.filter(client => !client.attributes.accepted).length)
  setConnectedCount(clientList.filter(client => client.attributes.connected).length)

  const openModal = () =>
  {
    setIsOpen(true)
    functionSetAttribute('.moreIcon', true)
  }

  const openLeaveModal = () =>
  {
    setLeaveModalIsOpen(true)
    functionalCTADLPush('display', 'leave cta', 'nievent')
  }

  const closeLeaveModal = (e) =>
  {
    setLeaveModalIsOpen(false)
  }

  const afterOpenModal = () =>
  {
    // references are now sync'd and can be accessed.
  }

  const afterOpenLeaveModal = () =>
  {
    const modalHeading = select('.leave-content .heading')
    modalHeading && focusElement(modalHeading)
  }

  const closeModal = () =>
  {
    setIsOpen(false)
    functionSetAttribute('.moreIcon', false)
    functionalCTADLPush('click', 'close_more_functionalities')
  }

  const functionSetAttribute = (clsName, flag) =>
  {
    const el = select(clsName)
    el ? el.setAttribute('aria-expanded', flag) : ''
  }

  const masterClassBAVideo = (ele, participant) => {
    const displayMsg = select('p', ele)
    displayMsg && displayMsg.remove()
    divEl = document.createElement('div')
    divEl.id = participant.sid
    divEl.classList.add('remote-media-wrapper')
  }



  const masterClassNonBAVideo = (ele, participant,participantType) => {
    const para = document.createElement('p')
    const name = participantName(participant.identity.split('$')[0])
    para.innerText = name
    para.classList.add('caller-name')
    ele.appendChild(para)
    ele.id = participant.sid
    ele.classList.add('small-video')
    if(participantType === 'assistant-ba'){
        para.classList.add('asst-ba-check')
    }
  }

 const addSwiperSlide = (newEl,participantType) => {
    const swiperDiv  = document.createElement('div')
    swiperDiv.classList.add('swiper-slide')
    let parentEl = select('.swiper-container')
    if(participantType === 'assistant-ba'){
      parentEl.swiper.prependSlide(swiperDiv)
     }else{
      parentEl.swiper.appendSlide(swiperDiv)
     }
    swiperDiv.appendChild(newEl)
  }

  const participantConnected = (participant) => {
    setUserIconText(a11yNotificationText(clientList))

    const participantType = participantTypeCheck(participant.identity)

    if (isMasterClass) {

      if (participantType === 'client' && !isBA) {
        return
      }

      if (participantType == 'main-ba') {
        remoteVideoContainer = select('.consultation__video')
        masterClassBAVideo(remoteVideoContainer, participant)
       }
       else {
        divEl = document.createElement('div')
        remoteVideoContainer = select('.swiper-wrapper')
        masterClassNonBAVideo(divEl, participant,participantType)
        addSwiperSlide(divEl,participantType)
        remoteVideoContainer = divEl
       }
     }
     else {
      remoteVideoContainer = select('.consultation__video')
      divEl = document.createElement('div')
      divEl.id = participant.sid
      divEl.classList.add('remote-media-wrapper')
    }

    if (isBA) {
      setTotalParticipant(totalParticipant + 1)
    }

    console.log('Participant "%s" connected', participant.identity.split('$')[0])
    participant.on('trackSubscribed', (track) => trackSubscribed(divEl, track, participant))
    participant.on('trackUnsubscribed', trackUnsubscribed)
    participant.on('trackUnsubscribed', trackUnsubscribed)

    participant.tracks.forEach((publication) =>
    {
      if (publication.isSubscribed) {
        trackSubscribed(divEl, publication.track, participant)
      }
    })

    remoteVideoContainer.classList.contains('consultation__video') ? remoteVideoContainer.appendChild(divEl) : ''

  }

  const openNotificationModal = () => {
    setUserIconText(a11yNotificationText(clientList))
    if(isMasterClass || clientList.length > 0) {
      setNotificationModalIsOpen(true)
    }
  }

  const closeNotificationModal = () =>
  {
    setNotificationModalIsOpen(false)
  }

  const onAcceptClicked = (participant) => {
    
    !isMasterClass && closeNotificationModal()

    const userIcon = getCollection('.notification-icon')
    userIcon.forEach((item) => {
      item ? item.setAttribute('aria-live', 'off') : ''
    })

    conversation.sendMessage(`accept$${participant.sid}`, { "type": "notification", "isBA": isBA })
    participant.updateAttributes({...participant.attributes, 'accepted': true}).then(() =>{
      setUserIconText(a11yNotificationText(clientList))
    })
    isMasterClass &&  select(".master-userlist__heading") && select(".master-userlist__heading").focus()
  }

  const participantDisconnected = (participant) =>
  {
    isBA ? setTotalParticipant(totalParticipant - 1) : ''
    console.log('Participant "%s" disconnected', participant.identity.split('$')[0])
    const removeEL = select(`#${participant.sid}`)
    removeEL && removeEL.parentNode.remove()

    if (isBA && clientList.length === 0) {
      setUserIconText(a11yNotificationText(clientList))
      closeNotificationModal()
    }
    if ((isMasterClass && participantTypeCheck(participant.identity) === "main-ba") || (!isMasterClass && participantTypeCheck(participant.identity) === "ba") ) {
      onFinalDisconnect()
    }
   updateSlides()
  }

  function trackSubscribed(div, track, participant = null)
  {
    if (track.kind !== 'data') {
      const El = track.attach()
      if (track.kind === 'video') {
	    const className= participantTypeCheck(participant.identity)
        if (!track.name.includes('camera')) {
          El.classList.add('screen-share')
          El.setAttribute('aria-label', screenShareA11y)
          El.setAttribute('tabindex', '-1')
        }
        else {
          El.setAttribute('aria-label', remoteVideoAriaLabel)
        }
        track.on('enabled', () =>
        {
          setAttributeHidden(El, false)
        })
        track.on('disabled', () =>
        {
          setAttributeHidden(El, true)
        })
        if (isDesktop && track.name.includes('isMobile') && !div.classList.contains('small-video')) {
          El.classList.add('remote-mobile')
        }
       participant ? El.classList.add(className) : ''
      }
      else if(track.kind === "audio"){
        El.setAttribute('aria-hidden', 'true')
        El.setAttribute('tabindex', '-1')
      }
       setTimeout(()=> {
        El.classList.contains('screen-share') ? screenSharedSelectDom(El) : select('div#'+participant.sid).appendChild(El)
      }, 500)

    }
  }

  const roomIsDisconnected = () =>
  {
    disconnectConversation()   // to disconnect user from the conversation if it exists
    const consultationContainer = select(".consultation__container")
    if (consultationContainer) {
      closeLeaveModal()
      consultationContainer.classList.add("is-hidden")
      showDomElements(false)
      props.onLeaveConfirmed()
      select(".twilio-content-container").classList.add("thankyou__wrapper")
      select(".hero__media").classList.add("hero__media__thankyou")
      !isBA && select(".hero__media").classList.add("hero__media__thankyou__client")
    }
    select(".hero__media").classList.add("hero__media__thankyou")
    !isBA && select(".hero__media").classList.add("hero__media__thankyou__client")
  }

  const detachTracksOnDisconnect = () =>
  {
    room.localParticipant.tracks.forEach(publication =>
    {
      trackUnsubscribed(publication.track)
      publication.track.kind !== 'data' && publication.track.stop()
    })
  }

  const setAttributeHidden = (El, flag) =>
  {
    flag ? El.setAttribute('hidden', true) : El.removeAttribute('hidden')
  }

  function trackUnsubscribed(track)
  {
    if (track.kind !== 'data') {
      track.detach().forEach((element) => element.remove())
    }
  }

  const participantTypeCheck = (participant) => {
	return participant.split('$')[2]
  }

  const participantName = (name) => {
    return name.replace(/^([^\s]+)\s(.+)/i,(_, first, last) => `${first} ${last.charAt(0)}.`)
  }

  const displayLocalParticipant = () =>
  {
    const participantType = participantTypeCheck(room.localParticipant.identity)
    if (isMasterClass) {
      if (participantType == 'main-ba') {
        localVideoContainer = select('.consultation__video')
        masterClassBAVideo(localVideoContainer, room.localParticipant)
       }
       else {
        const newEl = document.createElement('div')
        localVideoContainer = select('.swiper-wrapper')
        masterClassNonBAVideo(newEl, room.localParticipant,participantType)
        addSwiperSlide(newEl)
        localVideoContainer = newEl
       }
     }
     else {
      localVideoContainer = select('.caller-block')
      const localPaticipantName = select('.caller-name')
      localPaticipantName ? localPaticipantName.innerHTML = room.localParticipant.identity.split('$')[0] : ''
    }

    room.localParticipant.tracks.forEach((publication) =>
    {
      if (publication.track.kind !== 'data') {
        const El = publication.track.attach()
        if (publication.track.kind === 'video') {
          El.setAttribute('aria-label', localVideoAriaLabel)
          if (isMasterClass) {
            El.setAttribute('aria-label', mcVideoAriaLabel + " " +participantName(room.localParticipant.identity.split('$')[0]))
          }
          El.classList.add(participantType)
        }
        else if (publication.track.kind === 'audio') {
          El.setAttribute('aria-hidden', 'true')  
          El.setAttribute('tabindex', '-1')
        }
         El.style.cssText = "-moz-transform: scale(-1, 1); \
         -webkit-transform: scale(-1, 1); -o-transform: scale(-1, 1); \
         transform: scale(-1, 1); filter: FlipH;";
         localVideoContainer.appendChild(El)
       }
    })
    setLocalParticipantDisplayed(true)
  }

  const resizeHandler = () =>
  {
    setIsDesktop(isWideScreen())
    setIsMobile(isMobileScreen())
    adjustContainerHeight('.consultation__video')
  }

  const onLeaveButtonClick = () =>
  {
    !leaveModalIsOpen && openLeaveModal(true)
  }

  const onFinalDisconnect = () => {

    const twilioContainer = select(".twilio-content-container")
    twilioContainer && twilioContainer.classList.remove("chat__wrapper")
    detachTracksOnDisconnect()
    room.disconnect()
    localStorage.removeItem('proData')
    localStorage.removeItem('isCatalogSearchApiHit')
    roomIsDisconnected()
  }

  const onPrductCatalogClicked = () =>
  {
    isChatEnabled && setIsChatEnabled(false)
    setIsProdCatEnabled(!isProdCatEnabled)
    select('.consultation__container').classList.add('is-hidden')
    functionalCTADLPush('click', 'product_catalog')
  }

  const videoToggle = () =>
  {
    room.localParticipant.tracks.forEach((publication) =>
    {
      if (publication.track.kind === 'video' && publication.track.name.includes('camera')) {
        if (isVideoEnabled) {
          publication.track.disable()
          publication.track.stop()
        } else {
          publication.track.restart()
          publication.track.enable()
        }
      }
    })
    setIsVideoEnabled(!isVideoEnabled)
    functionalCTADLPush('click', 'camera')
  }

  const audioToggle = () =>
  {
    room.localParticipant.tracks.forEach((publication) =>
    {
      if (publication.track.kind === 'audio') {
        isMicEnabled ? publication.track.disable() : publication.track.enable()
      }
    })
    setIsMicEnabled(!isMicEnabled)
    functionalCTADLPush('click', 'microphone')
  }

  const backgroundToggle = () =>
  {
    room.localParticipant.tracks.forEach((publication) =>
    {
      if (publication.track.kind === 'video') {
        !isBackgroundEnabled ? addBackground(publication.track) : removeBackground(publication.track)
      }
    })

    setIsBackgroundEnabled(!isBackgroundEnabled)
    functionalCTADLPush('click', 'background')
    closeModal()
  }

  const stopSharing = (track) => {
    room.localParticipant.unpublishTrack(track)
    track.stop()
    setScreenTrack(null)
    toggleVideoforScreenSharing(true)
  }

  const screenShareToggle = () => {
   if (screenTrack == null) {
     navigator.mediaDevices.getDisplayMedia().then(stream => {
     setScreenTrack(stream.getTracks()[0])
     room.localParticipant.publishTrack(stream.getTracks()[0])
     toggleVideoforScreenSharing(false)
     stream.getTracks()[0].onended = () => { stopSharing(stream.getTracks()[0]) }
     }).catch(() => {
        toggleVideoforScreenSharing(true)
      })
    }
    else {
	 stopSharing(screenTrack)
    }
  }

 const toggleVideoforScreenSharing = (flag) => {
   room.localParticipant.tracks.forEach((publication) => {
     if (publication.track.kind === 'video' && publication.track.name.includes('camera')) {
     const localVideoEl = select('div#'+room.localParticipant.sid)
     const mainBaWrapper = select('video.main-ba')
       if (!flag) {
         publication.track.disable()
         publication.track.stop()
         localVideoEl ? select('video', localVideoEl).setAttribute('hidden', true) : ''
         isBA ? mainBaWrapper.setAttribute('hidden', true) : ''
       } else {
         publication.track.restart()
         publication.track.enable()
         localVideoEl ? select('video', localVideoEl).removeAttribute('hidden') : ''
         isBA ? mainBaWrapper.removeAttribute('hidden') : ''
       }
       setIsVideoEnabled(flag)
     }
   })
 }

 const screenSharedSelectDom = (El) => {
   const remoteWrapper1 = select('.remote-media-wrapper', select('.consultation__video'))
   const remoteWrapper2 = select('.consultation__video')
   if (remoteWrapper1) {
     remoteWrapper1.prepend(El)
   }
   else if (remoteWrapper2) {
     remoteWrapper2.prepend(El)
   }
 }

  const reverseCameraClicked = () =>
  {
    !isReverseCameraEnabled ? facingMode = 'environment' : facingMode = 'user'

    room.localParticipant.tracks.forEach(publication =>
    {
      if (publication.track.kind === 'video') {
        const videoTrack = publication.track
        videoTrack.restart({ facingMode: facingMode })
      }
    })

    setIsReverseCameraEnabled(!isReverseCameraEnabled)
    functionalCTADLPush('click', 'reverse_camera')
    closeModal()
  }

  const setInitialstateOfNotificationItems = () =>
  {
    room.localParticipant.tracks.forEach((publication) =>
    {
      if (publication.track.kind === 'video' && publication.track.name.includes('camera')) {
        setIsVideoEnabled(!publication.track.isStopped)

        if (publication.track.processor) {
          setIsBackgroundEnabled(true)
        }
      } else if (publication.track.kind === 'audio') {
        setIsMicEnabled(publication.track.isEnabled)
      }
    })
  }

  const closeClicked = (value) =>
  {
    setIsProdCatEnabled(value)
    select('.consultation__container').classList.remove('is-hidden')
    select('.productCatalog').focus()
  }

  const confirmExit = (event) =>
  {
    event.preventDefault()
    localStorage.removeItem('proData')
    localStorage.removeItem('isCatalogSearchApiHit')
    return (event.returnValue = '')
  }

  const listenToConversation = () =>
  {
    // to retrieve messages sent into the conversation before the participant connected
    conversation.getMessages().then(newMessages =>
    {
      console.log("message sent before joining - ", newMessages.items)
      setMessages(newMessages.items)
    })
    // to recieve messages sent into the conversation after the participant has connected
    conversation.on('messageAdded', handleMessageAdded)
  }

  const handleMessageAdded = async (message) =>
  {
    console.log(" received message object - ", message)
    const allyDiv = select('#msg-ally')

    if (message.attributes.type === "sharedProduct") {
      if (!isBA) {
        setShareProduct(JSON.parse(message.body))
        setShareMessageId(message.sid)
        setIsShareProductModalOpen(true)
      }
      console.log("Value of Message - ", JSON.parse(message.body), "sent by ", message.author)
      if (allyDiv) {
        const proDetails = JSON.parse(message.body)
        const shadeName = proDetails ? proDetails.selectedVariantData && proDetails.selectedVariantData.shade ? proDetails.selectedVariantData.shade : (proDetails.shade && proDetails.shade.shade) ? proDetails.shade.shade : '' : ''
        const priceValue = proDetails ? `${proDetails.price && proDetails.price.formattedValue ? proDetails.price.formattedValue : proDetails.price ? proDetails.price : ''}` : null
        allyDiv.innerHTML=`${isBA ? chatProdBAMsgClient : chatProductClientMessage} ${proDetails.name} ${shadeName} ${productRef} ${proDetails.code} ${priceValue} ${availableAct} ${addToCartCTA}`
      }
    }
    else if (allyDiv && message.attributes.type === "text") {
      console.log("Value of Message - ", message.body, "sent by ", message.author);
      if (allyDiv && message.attributes.type === "text") {
         const hostCombineTxt = headerText+" "+hostText
         allyDiv.innerHTML= `${message.attributes.isBA ? hostCombineTxt : message.author.split('$')[0]} ${chatMsgAuthorSpanTxt} ${message.body} ${getFormattedMsgTime(message.dateCreated)}`
      }
    }
    else if (!isBA && message.attributes.type === "notification"){
      console.log("Value of Message - ", message.body, "sent by ", message.author);
      const [action, participantId] = message.body.split('$')
      const myConvDetails = await getMyDetails()
      if(action === "remove" && participantId === myConvDetails.sid) {
        onFinalDisconnect()
      }
    }

    setMessages(oldMessages => [...oldMessages, message])
    console.log("updated message list - ", messages)
    setTimeout(() => {
      allyDiv.innerHTML = ''
    }, 500)
  }

  const chatToggle = () =>
  {
    setIsChatEnabled(!isChatEnabled)
    setIsFirstTimeOpen(false)
    functionalCTADLPush('click', 'chat')
  }

  const updatePriorityForBA = (kind, value) => {

    room.localParticipant.tracks.forEach(publication => 
      publication.track.kind === kind && publication.setPriority(value))
  }

  const localTrackPublished = () => {
    setInitialstateOfNotificationItems()
    isBA && updatePriorityForBA('video', 'high')
    displayLocalParticipant()

    !isBA && conversation.getParticipantByIdentity(chatClient.user.identity)
    .then(participant => {
      participant.updateAttributes({...participant.attributes, 'connected': true})
    })
  }

  const updateSlides = () => {
    const swiperEl = select('.swiper-container')
    if (swiperEl) {
      swiperEl.swiper.update()
      const count = select('.swiper-wrapper').childElementCount
      setTotalSlides(count)
    }
    setTimeout(()=> {
      setA11y()
    }, 500)
  }

  const setA11y = () => {
   const activeSlide = select('.swiper-slide-active')
   const activeSlidesContainer = select('.swiper-slide-active')
   const nextSlidesContainer = select('.swiper-slide-next')
   const carouselSlides = getCollection('.swiper-slide')
   const nextBtn = select('.swiper-button-next')
   const prevBtn = select('.swiper-button-prev')
   carouselSlides && carouselSlides.forEach(slide => {
    
     if (activeSlidesContainer.contains(slide) || nextSlidesContainer.contains(slide)) {
        slide.removeAttribute('aria-hidden')
        slide.removeAttribute('tabindex')
      } else {
        slide.setAttribute('aria-hidden', 'true')
        slide.setAttribute('tabindex', '-1')
      }
   })
   if (activeSlide) {
     activeSlide.setAttribute('tabindex', '-1')
     activeSlide.focus()
   }
    nextBtn && nextBtn.hasAttribute('disabled') ? nextBtn.setAttribute('hidden', true) : nextBtn.removeAttribute('hidden')
    prevBtn && prevBtn.hasAttribute('disabled') ? prevBtn.setAttribute('hidden', true) : prevBtn.removeAttribute('hidden')
  }

  const onOrientationChange = () => {
    if (isMobileScreen()) return
      const swiperEl = select('.swiper-container')
      window.orientation === 90 ?  swiperEl.swiper.changeDirection('vertical') : swiperEl.swiper.changeDirection('horizontal')
      updateSlides()
  }

  useEffect(() =>
  {
    adjustContainerHeight('.consultation__video')
    window.addEventListener('resize', resizeHandler)
    select('body').classList.add('has-modal')
    const conClose = select('.consultation-close')
    functionSetAttribute('.moreIcon', false)
    conClose ? conClose.focus() : ''
    window.addEventListener('beforeunload', confirmExit)
    window.addEventListener('unload', disconnectConversation)
    const swiperWrapper = select('.swiper-wrapper')
    if (swiperWrapper) {
      swiperWrapper.addEventListener('DOMNodeInserted', updateSlides)
      swiperWrapper.addEventListener('DOMNodeRemoved', updateSlides)
      window.addEventListener('orientationchange', onOrientationChange)
    }

    return () =>
    {
      window.removeEventListener('resize', resizeHandler)
      window.removeEventListener('beforeunload', confirmExit)
      window.removeEventListener('unload', disconnectConversation)
      if (swiperWrapper) {
        swiperWrapper.removeEventListener('DOMNodeInserted', updateSlides)
        swiperWrapper.removeEventListener('DOMNodeRemoved', updateSlides)
        window.removeEventListener('orientationchange', onOrientationChange)
      }

      select('body').classList.remove('has-modal')
    }
  }, [])

  useEffect(() =>
  {
    if (room) {
      if (!localParticipantDisplayed) {
        if (room.localParticipant.videoTracks.size > 0) {
          localTrackPublished()
        }
        else {
          room.localParticipant.on('trackPublished', publication => {
            if (publication.track.kind === 'video' ) {
              localTrackPublished()
            } 
          })
        }
    }
      room.participants.forEach(participantConnected)
      room.on('participantConnected', participantConnected)
      room.on('participantDisconnected', participantDisconnected)
      room.once('disconnected', (error) => {
        console.log("room.once disconnect")
        room.participants.forEach(participantDisconnected)})

      room.localParticipant.on('disconnected', (participant) => {
        console.log("local participant disconnect event received for -", participant.identity)
        onFinalDisconnect()
      })

    }

  }, [room])

  const chatClientStateChanged = (state) => {
    if (state === 'disconnected') {
      console.log(" == time to leave conversation ==");
      onFinalDisconnect()
    }
  }

  useEffect(() => {
    if (conversation) {
      listenToConversation()
      clientList.length > 0 && openNotificationModal()
    }

    chatClient.on('connectionStateChanged', chatClientStateChanged)

    return (() => {
      chatClient.off('connectionStateChanged', chatClientStateChanged)
    })
    

  }, [conversation])

  useEffect(() =>
  {
    messages && setMessages(messages)
  }, [messages])

  useEffect(() =>
  {
    isShareProductModalClose && hiddenRoomTitle && focusElement(hiddenRoomTitle.current)
  }, [isShareProductModalClose])

  useEffect(() =>
  {
    isShareProductModalOpen && !isBA && ecommerceAnalyticsDLPush(shareProduct, 'chat')
  }, [isShareProductModalOpen])

  useEffect(() =>
  {
    const consultationConatiner = select(".consultation__container")
    const hideBtn = select('.header__container_hideBtn')
    const returnBtn = select('.header__container_returnBtn')
    const twilioContainer = select(".twilio-content-container")
    const chatBtnElm = select('.chat-toggle')
    if (isChatEnabled) {
      twilioContainer && twilioContainer.classList.add("chat__wrapper")
      chatContainer && chatContainer.classList.remove("is-hidden")
      !isMobile && hideBtn && focusElement(hideBtn)
      isMobile && returnBtn && focusElement(returnBtn)
      isMobile && consultationConatiner && consultationConatiner.classList.add('is-hidden')
      chatContainer && chatContainer.hasAttribute('aria-hidden') && chatContainer.removeAttribute("aria-hidden")
      setTimeout(()=> {
        adjustContainerHeight('.chat__container__inner')
      }, 500) 
    } else {
      twilioContainer && twilioContainer.classList.remove("chat__wrapper")
      chatContainer && chatContainer.classList.add("is-hidden")
      isMobile && consultationConatiner.classList.remove('is-hidden')
      !isFirstTimeOpen && chatBtnElm && chatBtnElm.focus()
      isMobile && consultationConatiner && consultationConatiner.classList.remove('is-hidden')
      chatContainer && chatContainer.hasAttribute('aria-hidden') && chatContainer.setAttribute("aria-hidden")
    }

  }, [isChatEnabled])

  useEffect(() => {
    setUserIconText(a11yNotificationText(clientList))
  }, [clientList, userIconText]);

  return (
    <ShareProductContext.Provider value={{ shareProduct: shareProduct, setShareProduct: setShareProduct, shareMessageId: shareMessageId, setShareMessageId: setShareMessageId, isShareProductModalOpen: isShareProductModalOpen, setIsShareProductModalOpen: setIsShareProductModalOpen, isProdCatEnabled: isProdCatEnabled, setIsProdCatEnabled: setIsProdCatEnabled, isShareProductModalClose: isShareProductModalClose, setIsShareProductModalClose: setIsShareProductModalClose }}>
      <React.Fragment>
        <div className='consultation__container' aria-modal='true' role='dialog' aria-label={consultationAriaLabel} aria-hidden={isChatEnabled || leaveModalIsOpen || isShareProductModalOpen ? "true" : "false"} ref={consultationElm}>
          <div role='document'>
            <div className='consultation__header'>
              <div className='consultation__header-left'>
                <div className='text-quit'>
                  <Button onClick={onLeaveButtonClick} buttonText={consultationLeave} buttonClass='consultation-close' />
                  <Modal
                    isOpen={leaveModalIsOpen}
                    onAfterOpen={afterOpenLeaveModal}
                    onRequestClose={closeLeaveModal}
                    appElement={select('.twilio-content-container')}
                    contentLabel={leavePopinHeading}
                    portalClassName='leave-portal'
                    overlayClassName='leave-overlay'
                    className='leave-content'
                    shouldCloseOnOverlayClick={false}
                  >
                    <div role="document"> 
                      <div className='heading is-6' role='heading' aria-level='1'>
                        {isBA ? leavePopinHeadingBA : leavePopinHeading}
                      </div>
                      <p>{isBA ? leavePopinTextBA : leavePopinText}</p>
                      <div className='context'>
                        <Button buttonText={isBA ? leavePopinBtnConfirmBA : leavePopinBtnConfirm} buttonClass='is-primary is-block' onClick={onFinalDisconnect} />
                        <Button
                          buttonText={leavePopinBtnCancel}
                          buttonClass='is-secondary is-block'
                          onClick={() =>
                          {
                            closeLeaveModal()
                            functionalCTADLPush('click', 'cancel cta')
                          }}
                        />
                      </div>
                    </div>
                  </Modal>
                  <p role='heading' aria-level='1' className='is-sr-only' ref={hiddenRoomTitle}>
                    {consultationAriaLabel}
                  </p>
                </div>
              </div>
              {isBA && (
                <div className='consultation__header-right'>
                  <div className='user-icon'>
                    <UserIcon
                      onAcceptClicked={onAcceptClicked}
                      closeNotificationModal={closeNotificationModal}
                      openNotificationModal={openNotificationModal}
                    />
                  </div>
                </div>
              )}
            </div>
            <div className='consultation__body'>
              <div className='consultation__video'>{isBA && !totalParticipant && <p> {consultationText}</p>}</div>
              <div className='consultation__bar'>
                <ul className='bar-icons'>
                  {isBA && (
                    <li>
                      <Button onClick={onPrductCatalogClicked} spanClass='is-sr-only' iconName='catalog-twilio' buttonClass='productCatalog' buttonText={conSharePrd} iconSize='large' />
                    </li>
                  )}
                  <li>
                    <Button
                      onClick={audioToggle}
                      spanClass='is-sr-only'
                      buttonText={isMicEnabled ? conMicPhOff : conMicPhOn}
                      iconName={isMicEnabled ? 'microphone-on' : 'microphone-off'}
                      iconSize='large'
                    />
                  </li>
                  <li>
                    <Button
                      onClick={videoToggle}
                      spanClass='is-sr-only'
                      buttonText={isVideoEnabled ? conCamOff : conCamOn}
                      iconName={isVideoEnabled ? 'video-on' : 'video-off'}
                      iconSize='large'
                    />
                  </li>
                  {!isDesktop && !isBA && (
                    <li>
                      <Button
                        onClick={reverseCameraClicked}
                        spanClass='is-sr-only'
                        iconName='flip-camera'
                        buttonText={isReverseCameraEnabled ? conRevCamOff : conRevCamOn}
                        iconSize='large'
                      />
                    </li>
                  )}
                  <li>
                    <Button
                      buttonClass='chat-toggle'
                      onClick={chatToggle}
                      spanClass='is-sr-only'
                      iconName='twilio-chat'
                      buttonText={isChatEnabled ? conChatOff : conChatOn}
                      iconSize='large'
                    />
                  </li>
                  {!isBA && (
                    <li>
                      <Button
                        onClick={backgroundToggle}
                        spanClass='is-sr-only'
                        buttonText={isBackgroundEnabled ? conBgOff : conBgOn}
                        iconName={isBackgroundEnabled ? 'background-on' : 'background-off'}
                        iconSize='large'
                      />
                    </li>
                  )}
                  {isBA && (
                    <li>
                      <Button
                        onClick={() =>
                        {
                          openModal()
                          functionalCTADLPush('click', 'more_functionalities')
                        }}
                        spanClass='is-sr-only'
                        buttonText={conMoreFun}
                        buttonClass='moreIcon'
                        iconName='more-functions'
                        iconSize='large'
                      />
                    </li>
                  )}
                </ul>
                <div className='ReactModalPortal'></div>
              </div>
              <div className='consultation__caller'>
                 <div className={`caller-block ${isMasterClass ? 'master-caller-block' : ''} ${totalSlides > 2 && !isDesktopScreen() ? ' mobile-slider' : ''}`} data-participant = {totalSlides}>
                  {isMasterClass || (!isMasterClass && isBA) && <p className='caller-name'></p>}
                     <Swiper
                       direction={isDesktopScreen() ? 'vertical' : 'horizontal'}
                       slidesPerView={3}
                       spaceBetween={10}
                       slidesPerGroup={3}
                       navigation={{
                         prevEl: prevRef.current,
                         nextEl: nextRef.current
                       }}
                       onSlideChangeTransitionEnd={setA11y}
                       className={`mySwiper ${totalSlides < 4 ? 'fix-view' : ''}`}
                     >
	               </Swiper>
                   <div className="swiper-navigation js-carousel-navigation">
	                <button {...totalSlides < 4 && {hidden: true}} type="button" className='swiper-button swiper-button-prev js-carousel-prev' ref={prevRef} onClick={setA11y}>
	                  <Icon name={'next-white'} alt={prevLiveView} size={'large'} />
	                </button>
	                <button {...totalSlides < 4 && {hidden: true}} type="button" className='swiper-button swiper-button-next js-carousel-next' ref={nextRef} onClick={setA11y}>
	                  <Icon name={'next-white'} alt={nextLiveView} size={'large'} />
	                </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <Modal
            isOpen={modalIsOpen}
            onAfterOpen={afterOpenModal}
            onRequestClose={closeModal}
            appElement={select('.twilio-content-container')}
            contentLabel={conMoreFun}
            portalClassName='more-portal'
            overlayClassName='more-overlay'
            className='more-content'
          >
            <CloseButton onClick={closeModal} buttonClass='more-option-close' buttonText={`${closeText} - ${conMoreFun}`} iconSize='small' />
            <div>
              <ul className='more-options-list'>
                {isMasterClass && isDesktop && isBA && <li>
                  <Button onClick={screenShareToggle}
                    buttonText={screenTrack ? screenShareStop : screenShareStart}
                    iconName="external_link"
                    ariaLive='polite'
                    iconSize='medium' />
                </li>}
                <li>
                  <Button onClick={backgroundToggle}
                    buttonText={isBackgroundEnabled ? conBgOff : conBgOn}
                    iconName={isBackgroundEnabled ? 'background-on' : 'background-off'}
                    iconSize='large' />
                </li>
                {!isDesktop && <li>
                  <Button
                    onClick={reverseCameraClicked}
                    iconName='flip-camera'
                    buttonText={isReverseCameraEnabled ? conRevCamOff : conRevCamOn}
                    iconSize='large'
                  />
                </li>}
              </ul>
            </div>
          </Modal>
        </div>
        {isProdCatEnabled && (
          <ProductCatalog
            closeClicked={closeClicked}
            totalParticipant={totalParticipant}
            onAcceptClicked={onAcceptClicked}
            closeNotificationModal={closeNotificationModal}
            openNotificationModal={openNotificationModal}
            conMoreFun={conMoreFun}
          />
        )}
        {isShareProductModalOpen && <ShareProductModal />}
        <ChatContext.Provider value={{ messages: messages, setMessages: setMessages, isChatEnabled: isChatEnabled }}>
          {<Chat
            totalParticipant={totalParticipant}
            onAcceptClicked={onAcceptClicked}
            closeNotificationModal={closeNotificationModal}
            openNotificationModal={openNotificationModal} />}
        </ChatContext.Provider>
      </React.Fragment>
    </ShareProductContext.Provider>
  )
}

export default ChanelTwilioConsultation