import React, { useContext, useState, useEffect } from 'react'
import { select } from 'lib/selectors'
import Modal from 'react-modal'
import { onOpenModal, onCloseModal } from '../Utils/helper'
import { ShareProductContext } from './chanelTwilioConsultation'
import ShareProductModalContent from './shareProductModalContent'

const ShareProductModal = () =>
{

  const { productRef } = jsConfigSecond.twilio
  const { shareProduct, isShareProductModalOpen, setIsShareProductModalOpen, setIsShareProductModalClose } = useContext(ShareProductContext)
  const shadeName = shareProduct.selectedVariantData && shareProduct.selectedVariantData.shade ? shareProduct.selectedVariantData.shade : (shareProduct.shade && shareProduct.shade.shade) ? shareProduct.shade.shade : ''
  const ariaLabelMainHeading = `${shareProduct.name ? shareProduct.name : ''} ${shadeName} ${productRef} ${shareProduct.code}`

  useEffect(() => {
   return () => {
    onCloseModal()
    }
  }, [])

  return (
    <Modal
      closeTimeoutMS={200}
      isOpen={isShareProductModalOpen}
      onAfterOpen={onOpenModal}
      appElement={select('.twilio-content-container')}
      contentLabel={ariaLabelMainHeading}
      portalClassName='share-product'
      overlayClassName='share-product__overlay'
      className='share-product__content'
      shouldCloseOnOverlayClick={true}
    >
      <div role='document' className="share-product-doc">
        <ShareProductModalContent />
      </div>
    </Modal>
  )
}

export default ShareProductModal