import React, { useContext } from 'react'
import Button from '../button'
import ParticipantListElement from '../participantListElement'
import { checkParticipantStatus } from 'ui/twilio/Utils/helper'
import { VideoContext } from '../chanelTwilioVideo'

const MasterClassClientList = (props) => {

  const { onClick } = props
  const { clientWaitingTxt, clientJoinedTxt } = jsConfigSecond.twilio
  const { clientList ,userIconText, connectedCount } = useContext(VideoContext)


  return (
    <div className="master-userlist">
      <p className="is-sr-only master-userlist__heading" tabIndex="-1" role="heading" aria-level="1">{userIconText}</p> 
      
      {/* property key for Accept all text exists */}

      { (clientList.length - connectedCount > 0) && 
        <>
          <p className="is-sr-only" role="heading" aria-level="2">{clientWaitingTxt}</p>
          <ol className="master-userlist__waiting">
            {clientList.map((participant, index) =>
              !checkParticipantStatus(participant, 'connected') && 
              <ParticipantListElement participant={participant} onClick={() => onClick(participant)} key={'participant-' + index} />
            )}
          </ol>
        </>
      }
      

      {(connectedCount > 0 && (clientList.length - connectedCount) > 0) && <hr className="list-separator" />}

      {connectedCount > 0 &&
        <>
          <p className="is-sr-only" role="heading" aria-level="2">{clientJoinedTxt}</p>
          <ol className="master-userlist__accepted">

            {clientList.map((participant, index) =>
              (checkParticipantStatus(participant, 'connected')) && 
              <ParticipantListElement participant={participant} connected={true} key={'participant-' + index} />
            )}
          </ol>
        </>
      }
      
    </div>
  )
}

export default MasterClassClientList
