import React, { useContext, useEffect, useState } from 'react'
import Icon from './Icon'
import Button from './button'
import { checkParticipantStatus, displayName } from '../Utils/helper'
import { VideoContext } from './chanelTwilioVideo'
import RemoveConfirmationModal from './master-class/removeConfirmationModal'

const ParticipantListElement = (props) => {

  const { participant, onClick, connected } = props
  const {acceptBtn, btnRemove, isBA, muteA11yText, unMuteA11yText } = jsConfigSecond.twilio
  const { room, conversation, setNotificationModalIsOpen } = useContext(VideoContext)
  const [ isMicEnabled, setIsMicEnabled ] =  useState(false)
  const [showRemoveModel,setShowRemoveModel] = useState(false)

  const participantDisplayName = displayName(participant.identity.split('$')[0])
  const nameStr = participant.identity.split('$')[0].replace(/\s/g, '-')
  const uniqueParticipantId = nameStr.substr(0,5) + participant.sid.substr(0,5)

  function microphoneState (convIdentity) {
    const roomParticipant = Array.from(room.participants.values()).filter((participant) => participant.identity == convIdentity )

    roomParticipant.length > 0 && roomParticipant[0].tracks.forEach((publication) => {
      if (publication.track && publication.track.kind === "audio") {
        publication.track.isEnabled && setIsMicEnabled(true)
        publication.track.on('enabled', () => setIsMicEnabled(true))
        publication.track.on('disabled', () => setIsMicEnabled(false))
      }
    })
  }

  const openRemoveModal = () => setShowRemoveModel(true)

  const closeRemoveModel = () => {
    setShowRemoveModel(false)
    setNotificationModalIsOpen(false)
  }

  const handleRemoveParticipant = () => {
    conversation.sendMessage(`remove$${participant.sid}`, { "type": "notification", "isBA": isBA })
    setShowRemoveModel(false)
    setNotificationModalIsOpen(false)
  }

  useEffect(() => {
    connected && microphoneState(participant.identity)
  }, [connected])

  return (
     <li>
      <div className="paticipant-container" tabIndex = '-1'> 
      <Icon name={'account'} size={'large'} />
      <span className="part-name" id={uniqueParticipantId}>{participantDisplayName}</span>
        { !connected ? 
        
        !checkParticipantStatus(participant, 'connected') && <Button
          ariaDescribedby={uniqueParticipantId}
          onClick={onClick}
          iconSize="large"
          buttonText={acceptBtn} 
          buttonClass="accept"
          ariaDisabled={checkParticipantStatus(participant, 'accepted') ? 'true' : 'false'}
        />
      :
      <>
        <Button
          onClick={openRemoveModal}
          ariaDescribedby={uniqueParticipantId}
          buttonText={btnRemove}
          buttonClass="remove"
        />
        <Icon
          name={isMicEnabled ? "microphone-on" : "microphone-off"}
          size='large'
          alt={isMicEnabled ? ` ${unMuteA11yText}` : ` ${muteA11yText}` }
        />
        {showRemoveModel && <RemoveConfirmationModal
          name={participantDisplayName}
          showRemoveModel={showRemoveModel}
          closeRemoveModel={closeRemoveModel}
          handleRemoveParticipant={handleRemoveParticipant}

        />}
      </>
      }
        </div>
    </li>
  )
}

export default ParticipantListElement
