import React, { useEffect, useState } from 'react'
import { select } from 'lib/selectors'
import ChatBody from './chatBody'
import ChatFooter from './chatFooter'
import ChatHeader from './chatHeader'
import { adjustContainerHeight } from '../../Utils/helper'

function Chat(props)
{
	const { titleText } = jsConfigSecond.twilio

	const resizeHandler = () => {
		adjustContainerHeight('.chat__container__inner')
	}

	useEffect(() =>
	{
		adjustContainerHeight('.chat__container__inner')
		window.addEventListener('resize', resizeHandler)
		window.addEventListener('keydown', handleKeyDown)
		return () =>
		{
			window.removeEventListener('resize', resizeHandler)
			window.removeEventListener('keydown', handleKeyDown)
		}
	}, []);

	const handleKeyDown = (e) =>
	{
		let isTabPressed = e.key === 'Tab' || e.keyCode === 9
		if (!isTabPressed) {
			return
		}
		const firstFocusableElement = select('.header__container_hideBtn')
		let lastFocusableElement = select('.footer__container_sendMsg')
		if (firstFocusableElement && lastFocusableElement) {
			if (e.shiftKey) { // if shift key pressed for shift + tab combination
				if (document.activeElement === firstFocusableElement) {
					lastFocusableElement.focus() // add focus for the last focusable element
					e.preventDefault()
				}
			} else { // if tab key is pressed
				if (document.activeElement === lastFocusableElement) { // if focused has reached to last focusable element then focus first focusable element after pressing tab
					firstFocusableElement.focus() // add focus for the first focusable element
					e.preventDefault()
				}
			}
		}
	}

	return (
		<div className="chat__container is-hidden" role="dialog" aria-label={titleText}>
			<div className="chat__container__inner" role="document">
				<ChatHeader {...props} />
				<ChatBody totalParticipant={props.totalParticipant}/>
				<ChatFooter />
			</div>
		</div>
	)
}

export default Chat