const ecommerceAnalyticsDLPush = (products, type = 'thankYouPage') =>
{
  if (type == 'addToCart') {
    const addToCartAnalyticsObject = {
      cartId: '',
      event: 'uaevent',
      eventCategory: 'add to cart',
      eventAction: 'regular quickbuy',
      action: '',
      ecommerce: {
        currencyCode: window.dataLayer[0].currencyCode,
        add: {
          products: fetchAnalyticsProdObj(products, type)
        },
      }
    }
    Array.from(products).map((product)=>{
      addToCartAnalyticsObject.eventLabel = product.gaProductName
    })
    dataLayer.push(addToCartAnalyticsObject)
    
  } else if (type == 'chat') {
    const chatAnalyticsObject = {
      eventCategory: 'product impressions',
      eventAction: `${window.dataLayer[0].page_name}/${window.dataLayer[0].category}/${window.dataLayer[0].sub_category_level2}`,
      eventLabel: '',
      ecommerce: {
        currencyCode: window.config.currentCurrency,
        impressions: fetchAnalyticsProdObj(products, type)
      }
    }
    dataLayer.push(chatAnalyticsObject)
  }
  else {
    const addToCartAnalyticsThankYouPageObject = {
      cartId: '',
      cartValue: '',
      page_type: 'online consultation - thank you',
      event: 'nievent',
      eventCategory: 'enhancedecommerce',
      eventAction: 'product list view',
      sub_category_level1: 'thank you page',
      event: 'virtualpageview',
      division: 'fnb',
      category: 'thank you page',
      divisionFlag: 'fnb',
      page: `${window.location.pathname}thank-you${window.location.search}`,
      ecommerce: {
        currencyCode: window.config.currentCurrency,
        impressions: fetchAnalyticsProdObj(products, type)
      }
    }
    dataLayer.push(addToCartAnalyticsThankYouPageObject)
  }
}


function fetchAnalyticsProdObj(products, type)
{

  const analyticsProdObj = []
  if (Array.isArray(products) === false) {
    const obj = {
      ...(type == 'thankYouPage' ? { list: `online consultation - thank you/${products.axisType}/online consultation/thank you page`, } : ''),
      name: products.gaProductName,
      id: products.productID,
      price: `${products.price && products.price.formattedValue ? products.price.formattedValue : products.price ? products.price : ''}`, //$unit_price
      brand: products.division,
      category: `${products.division}/${products.axisType}/${products.productCategory}/${products.productSubCategory}`,
      variant: products.gaVariant,
      quantity: '1',
      dimension8: products.code,
      dimension17: 'no',
      dimension18: '',
      dimension20: '',
      dimension21: products.outOfStockStatus,
      dimension25: products.axisType,
      dimension45: getDimension45(products),
      dimension65: getDimension65(products),
    }
    analyticsProdObj.push(obj)
  } else {
    Array.from(products).map((product, index) =>
    {
      const obj = {
        ...(type == 'thankYouPage' ? { list: `online consultation - thank you/${product.axisType}/online consultation/thank you page`, } : ''),
        name: product.gaProductName,
        id: product.productID,
        price: `${product.price && product.price.formattedValue ? product.price.formattedValue : product.price ? product.price : ''}`, //$unit_price
        brand: product.division,
        category: `${product.division}/${product.axisType}/${product.productCategory}/${product.productSubCategory}`,
        variant: product.gaVariant,
        ...(type == 'addToCart' ? { quantity: '1' } : { position: index + 1 }),
        dimension8: product.code,
        dimension17: 'no',
        dimension18: '',
        dimension20: '',
        dimension21: product.outOfStockStatus,
        dimension25: product.axisType,
        dimension45: getDimension45(product),
        dimension65: getDimension65(product),
      }
      analyticsProdObj.push(obj)
    })
  }
  return analyticsProdObj
}

function getDimension45(product)
{

  if (product.exclusive) {
    return 'EXCLUSIVE'
  } else if (product.newProduct) {
    return 'NEW'
  } else if (product.limitedEdition) {
    return 'LIMITED'
  } else if (product.foreignEdition) {
    return 'FOREIGN'
  } else if (product.numberedEdition) {
    return 'NUMBERED'
  }
  return 'None'

}

function getDimension65(product)
{
  if (product.productSegment && product.productSegment.toLowerCase() === 'cs') {
    return 'Yes'
  } else if (product.productSegment && product.productSegment.toLowerCase() === 'ncs') {
    return 'No'
  }
  return ''
}

export
{
  ecommerceAnalyticsDLPush
}
