import React from 'react'
import { render } from 'react-dom'
import { select } from 'lib/selectors'
import { Provider } from 'react-redux'
import * as xhr from "modules/fetch"
import configureStore from './Redux/configureStore'
import bind from 'delegate'
import ChanelTwilioVideo from './Components/chanelTwilioVideo'
import { activateChat, hideDomElements } from './Utils/helper'
import Loader from 'ui/widgets/loader'


const store = configureStore()
const twilioWrapper = select('.twilio-content-container')
let selectedRole = ''
let enteredName = ''
const {roomId, clientEmailVerify, isMasterClass, checkAdvisor, nameRequiredError, incorrectFormatError, selectedRoleError, isMainBASelected, isAssistantBASelected, baName, isBA, purchaseRef } = jsConfigSecond.twilio
const nameRegex = new RegExp("^[a-zA-Z--- ,.'-]+$")
let errorField
let loader

function handleSubmit(event) {
  event.preventDefault()
  const inputField = select("#onlineConsultation-email")
  if (inputField) {
    const requestdata = {
	  "purchaseRef" : purchaseRef,
      "room-id": roomId,
      "user-id": inputField.value
    }
    xhr.get(clientEmailVerify, { data: requestdata })
      .then((res) => {
        if (res.error === 'false') {
          if (inputField.getAttribute("aria-describedby") != null) {
            inputField.removeAttribute("aria-describedby")
          }
          const twilioBox = select(".twilio-main-box")
          twilioBox ? twilioBox.classList.add('is-hidden') : ''

          import('twilio-video').then((Video) => {
            console.log('Video NPM package loaded')
            document.body.classList.add('online-consultation')
            console.log('Status of Twilio Video support: ', Video && Video.isSupported)
              activateChat(res.token, res.roomID).then((conversation) => {

              render(
                <Provider store={store}>
                  <ChanelTwilioVideo
                    appointmentDate={res.appointmentDate}
                    appointmentTime={res.appointmentTime}
                    currentTimestamp={Date.now()}
                    serviceName={res.serviceName}
                    twilioToken={res.token}
                    conversation={conversation}
                  />
                </Provider>,
                twilioWrapper
                )
              })
              .catch((err) => console.error(err))

          }).catch(err => console.error(err))
          
        } else {
          const formEl = event.target.form
          const errorEl = select('p.error', formEl)
          if (errorEl !== null) errorEl.remove()
          inputField.setAttribute("aria-describedby", "email-error-message")
          inputField.focus()
          errorHandling("email-error-message", res.errorMsg)
        }
      })
      .catch(err => console.error(err))
  }
}

function errorHandling(id, message,errorEL=null) {
		let formField = errorEL ? select(errorEL) : select('.field')
    if (formField) {
      formField.classList.add('has-error')
      errorField = document.createElement('p')
      errorField.classList.add('error')
      errorField.setAttribute('id', id)
      errorField.setAttribute('tabindex', '-1')
      errorField.classList.remove('is-hidden')
      errorField.innerHTML = message
      formField.appendChild(errorField)
    } else {
      if (formField) { formField.classList.remove('has-error') }
      errorField = formField.select('.error')
      if (errorField) {
        errorField.classList.add('is-hidden')
        errorField.innerHTML = ''
        formField.removeChild(errorField)
      }
   }
}

function grantAccessToMedia(e) {

  const beginButton = select("#check-media-access-button")
  if (beginButton.getAttribute('aria-disabled') ) {
    e.preventDefault()
    return
  }
  
	if (isMasterClass) {
		const inputField = select("#entered-name")
		const formField = select('.field')
		const errorEl = select('p.error')
		enteredName = enteredName.trim()
		
		if (errorEl !== null) {
			if (inputField.getAttribute("aria-describedby") != null) {
				inputField.removeAttribute("aria-describedby")
				inputField.removeAttribute("aria-invalid")
			}
			formField.classList.remove('has-error')
			errorEl.remove()
		}
		
		const error = !enteredName ? nameRequiredError : (!nameRegex.test(enteredName) && incorrectFormatError)
		if (error) {
			inputField.setAttribute("aria-describedby", "name-error-message")
			inputField.setAttribute("aria-invalid", true)
			inputField.focus()
			errorHandling("name-error-message",error)
		}
		else {
			const requestdata = {
				"room-id": roomId,
				"ba-name": enteredName,
				"ba-role": selectedRole
			}
			invokeCheckAdvisor(requestdata)
		}
	}
	else {

		const requestdata = {
			"room-id": roomId,
			"ba-name": baName,
		}
		invokeCheckAdvisor(requestdata)
	}

	function initializeTwilioVideo(res) {
		activateChat(res.token,res.roomID)
		.then((conversation) => {
      console.log("conversation package loaded");
    	import('twilio-video').then((Video) => {
            console.log('Video NPM package loaded')

            document.body.classList.add('online-consultation')

            console.log('Status of Twilio Video support: ', Video && Video.isSupported)

            render(
              <Provider store={store}>
                <ChanelTwilioVideo currentTimestamp={Date.now()}
                  twilioToken={res.token} 
                  conversation={conversation} />
              </Provider>,

              twilioWrapper
            )

            hideDomElements()
          })

          .catch((err) => console.error(err))
      })

      .catch((err) => console.error(err))

      .finally(function () {
        loader && loader.destroy()
      })
  }

  function invokeCheckAdvisor(requestdata) {
    const button = select('#check-media-access-button')
		const formField = select('.role-formField ')
		const errorE1 = select('p.error', formField)
	
    loader = new Loader(button, {
      button: true
    })
    loader.init()
    loader.create()

    xhr.get(checkAdvisor, { data: requestdata })
      .then((res) => {     
				if (res.error === 'false') {
          initializeTwilioVideo(res)
        } else {
          if (errorE1 !== null) errorE1.remove()
            formField.setAttribute('aria-describedby', 'role-error-message')
            formField.focus()
            errorHandling('role-error-message', selectedRoleError, '.role-field')
            loader && loader.destroy()
        }	
      })
      .catch(err => {
        console.error(err)
        loader && loader.destroy() 
    })
      
  }
 
}

function closeListContainer(e, action='none') {
   window.requestAnimationFrame(() => {
     const partDataEl = select('.participant-data')
     const partContentEl = select('.participant_content')

     const roleField = select('.role-field')
     const roleContentEl = select('.role_content')

       if ((partDataEl && (!e.keyCode || e.keyCode === 9) && !partDataEl.contains(document.activeElement) && partDataEl.classList.contains('is-active')) || action === 'close' ) {
         partDataEl.classList.remove('is-active')
         select('.js-collapse-button', partDataEl).setAttribute('aria-expanded', 'false')
         partContentEl.setAttribute('aria-hidden', 'true')
         partContentEl.style.display='none'
       }
       if ((roleField && (!e.keyCode || e.keyCode === 9) && !roleField.contains(document.activeElement) && roleField.classList.contains('is-active')) || action === 'close') {
        roleField.classList.remove('is-active')
        select('.js-collapse-button', roleField).setAttribute('aria-expanded', 'false')
        roleContentEl.setAttribute('aria-hidden', 'true')
        roleContentEl.style.display='none'
      }
    })
 }

function handleRoleSelect(e) {

  selectedRole = e.target.value
  select(".role_button__wrapper").innerHTML = selectedRole
  if(enteredName && selectedRole) {
    handleBeginButtonState('enabled')
  } else {
    handleBeginButtonState('disabled')
  }

  closeListContainer(e, "close")
  select('button', select('.role-data')).focus()

}

function handleEnteredName(e) {
  enteredName = e.target.value
  if(enteredName && selectedRole) {
    handleBeginButtonState('enabled')
  } else {
    handleBeginButtonState('disabled')
  }

}

function handleBeginButtonState(state) {
  const beginButton = select("#check-media-access-button")
  if (state === 'disabled') {
    beginButton.setAttribute('aria-disabled', true)
  } else {
    beginButton.removeAttribute('aria-disabled')
  } 
}

function addClickEventForAvailableRoles() {
  if (!isMainBASelected) {
    select('.main-ba').addEventListener('click', handleRoleSelect)
  }

  if (!isAssistantBASelected) {
    select('.assistant-ba').addEventListener('click', handleRoleSelect)
  } 

}

export default function oc() {

  if (isMasterClass && isBA) {
    handleBeginButtonState('disabled')
    addClickEventForAvailableRoles()
  } 
  bind(document.body, '[id="twilio-launch-button"]', "click", handleSubmit);
  isBA && bind(document.body, '[id="check-media-access-button"]', "click", grantAccessToMedia);
  bind(document.body, '.participant-data', "keydown", closeListContainer)
  bind(document.body, '.role-field', "keydown", closeListContainer)
  bind(document.body, 'body', "click", closeListContainer)
  bind(document.body, '[id="entered-name"]', "keyup", handleEnteredName);
}
