import React, { useState, useContext, Fragment, useEffect, useRef } from "react"
import Icon from "../Icon"
import { ChatContext, ShareProductContext } from '../chanelTwilioConsultation'
import ShareProductModalContent from "../shareProductModalContent"
import { ecommerceAnalyticsDLPush } from '../../Utils/analytics'
import { select } from 'lib/selectors'
import { getFormattedMsgTime } from '../../Utils/helper'
import { getOS } from "lib/helpers"


function ChatBody(props)
{
  const { headerText, hostText, chatBodyAriaLabel, chatMsgAuthorSpanTxt } = jsConfigSecond.twilio

  //react contexts
  const { messages, isChatEnabled } = useContext(ChatContext)

  //react states
  const [chatBodyAriaLive, setChatBodyAriaLive] = useState("")

  //react refs
  const messageEl = useRef(null)

  const attributes = {
    ...( getOS() != 'Mac OS' ? { 'role': 'log' } : {} ),
    ...( getOS() != 'Mac OS' ? { 'aria-live': chatBodyAriaLive } : {} ),
  }

  const chatWindowScrollDown = (event = null) => {
    // isChatMsgEvent will strictly monitor for only events that add msg to chat and no other DOM manipulations
    const isChatMsgEvent = event ? event.target.classList && event.target.classList.contains('chat_Message') : true
    if (messageEl && isChatMsgEvent) {
      setTimeout(() => {
        const lastMsg = select('.chat_Message:last-child')
        lastMsg &&  lastMsg.scrollIntoView({ behavior: 'smooth', block: 'end' });
      }, 0)
    }
  }

  useEffect(() =>
  {
    isChatEnabled ? setChatBodyAriaLive("polite") : setChatBodyAriaLive("off")
    if (isChatEnabled && messages) {
      let productMsgs = messages.filter(item => item.attributes.type === "sharedProduct")
      let productMsgsBody = productMsgs.map(item => JSON.parse(item.body))
      ecommerceAnalyticsDLPush(productMsgsBody, 'chat')
      chatWindowScrollDown()
    }
  }, [isChatEnabled])

  useEffect(() => {
    messageEl &&  messageEl.current.addEventListener('DOMNodeInserted', chatWindowScrollDown)
  }, [])


  //react functions
  const buildChatMessages = (chatMessage, chatMsgIndex) =>
  {
    const chatmessageId = `chat-message-${chatMsgIndex}`
    const chatMsgtimeId = `chat-message-time-${chatMsgIndex}`
    if (chatMessage.attributes.type === "text") {
      return (
        <Fragment key={chatMsgIndex}>
          <div className={chatMessage.attributes && chatMessage.attributes.isBA ? "chat_Message chatBody__container__hostMsg" : "chat_Message chatBody__container__clientMsg"}>
            <div className={chatMessage.attributes && chatMessage.attributes.isBA ? "chatBody__container__hostMsg__header" : "chatBody__container__clientMsg__header"}>
              {chatMessage.attributes && chatMessage.attributes.isBA && <p id={chatMessage.participantSid} className="chatBody__container__hostMsg__header__title"><span>{headerText}</span> {chatMessage.attributes && chatMessage.attributes.isBA && <span className="chatBody__container__hostMsg__header__hostMsg">{hostText}</span>}<span className="is-sr-only">{chatMsgAuthorSpanTxt}</span></p>}
              {chatMessage.attributes && !chatMessage.attributes.isBA && <p id={chatMessage.participantSid} className="chatBody__container__clientMsg__header__clientName">{chatMessage.author.split('$')[0]}<span className="is-sr-only">{chatMsgAuthorSpanTxt}</span></p>}
            </div>
            <p id={chatmessageId} className={chatMessage.attributes && chatMessage.attributes.isBA ? "chatBody__container__hostMsg__header__msgBody" : "chatBody__container__clientMsg__header__msgBody"}>{chatMessage.body}</p>
            <p id={chatMsgtimeId} className={chatMessage.attributes && chatMessage.attributes.isBA ? "chatBody__container__hostMsg__header__msgTime" : "chatBody__container__clientMsg__header__msgTime"}>{getFormattedMsgTime(chatMessage.dateCreated)}</p>
          </div>
        </Fragment>
      )
    } else if (chatMessage.attributes.type === "sharedProduct"){
      return (
        <div className={chatMessage.attributes && chatMessage.attributes.isBA ? "chat_Message chatBody__container__product share-product__content chatBody__container__hostMsg" : "chat_Message share-product__content share-product__content chatBody__container__clientMsg"} key={chatMsgIndex}>
          <ShareProductModalContent isShareProductMessage={true} shareProductMessage={JSON.parse(chatMessage.body)} shareProductMessageId={chatMessage.sid} msgTime={chatMessage.dateCreated} totalParticipant={props.totalParticipant} />
        </div>)
    }
  }


  return (
    <div className="chatBody__container" ref={messageEl} tabIndex="0" role="region" aria-label={chatBodyAriaLabel}>
      <div {...attributes}>
        {messages && messages.map((item, index) => buildChatMessages(item, index))}
      </div>
    </div>
  )
}

export default ChatBody