import React, { useEffect, useRef } from 'react'
import Icon from './Icon'
const Button = (props) =>
{
  const { buttonClass, id, onClick, onBlur, onFocus, disabled, iconName, iconSize, spanClass, buttonText, ariaDescribedby, type, ariaLabel, ariaLive, ariaDisabled, dataPopupTarget, dataPopupQuery, ariaHasPopup, dataModalTitle, dataTrackWishlist, dataWishlist, dataTextActive, dataTextInactive, dataTextWishlistInactive, dataTextWishlistActive } = props
  const buttonRef = useRef()

  useEffect(() => {
    if (id === 'check-media-access-button') {
      buttonRef.current.focus()
    }
    if (buttonClass === 'close__button') {
      buttonRef.current.focus()
    }
    return () => { }
  }, [])

  return (
    <button
      type={type ? type : 'button'}
      ref={buttonRef}
      className={`button ${buttonClass ? buttonClass : ''}`}
      {...(id ? { id: id } : {})}
      {...(ariaLabel ? { 'aria-label': ariaLabel } : {})}
      {...(ariaDescribedby ? { 'aria-describedby': ariaDescribedby } : {})}
      {...(ariaDisabled ? { 'aria-disabled': ariaDisabled } : {})}
      {...(dataPopupTarget ? { "data-popup-target": dataPopupTarget } : {})}
      {...(dataPopupQuery ? { "data-popup-query": dataPopupQuery } : {})}
      {...(ariaHasPopup ? { "aria-haspopup": ariaHasPopup } : {})}
      {...(ariaLive ? { 'aria-live': ariaLive } : {})}
      {...(dataModalTitle ? { "data-modal-title": dataModalTitle } : {})}
      {...(dataTrackWishlist ? { 'data-track-wishlist': dataTrackWishlist } : {})}
      {...(dataWishlist ? { 'data-wishlist': dataWishlist } : {})}
      {...(dataTextActive ? { 'data-text-active': dataTextActive } : {})}
      {...(dataTextInactive ? { 'data-text-inactive': dataTextInactive } : {})}
      {...(dataTextWishlistInactive ? { 'data-text-wishlist-inactive': dataTextWishlistInactive } : {})}
      {...(dataTextWishlistActive ? { 'data-text-wishlist-active': dataTextWishlistActive } : {})}

      disabled={disabled}
      onClick={onClick}
      onBlur={onBlur}
      onFocus={onFocus}
    >

      {iconName && <Icon name={iconName} size={iconSize} />}
      {buttonText && <span className={spanClass}>{buttonText}</span>}
    </button>
  )
}

export default Button