import React, { useEffect, useState, useContext } from 'react'
import { select } from 'lib/selectors'
import Button from './button'
import { VideoContext } from './chanelTwilioVideo'
import { disconnectConversation, functionalCTADLPush, getConversationParticipant, getMyDetails, hideDomElements, updateGADataForClient } from './../Utils/helper'


const WaitingRoom = (props) => {
  const { waitingRoomFooterHeading, waitingRoomFooterP1, waitingRoomFooterP2, waitingRoomFooterP3, waitingRoomFooterP4, waitingRoomHeader, waitingRoomHeaderAccepted, waitingRoomText, heading, text, startCTA } = jsConfigSecond.twilio
  const [appointmentTime, setAppointmentTime] = useState(props.appointmentTime)
  const [appointmentDate, setAppointmentDate] = useState(props.appointmentDate)
  const [appointmentType, setAppointmentType] = useState(props.serviceName)
  const { conversation, hasBAAccepted, setHasBAAccepted }= useContext(VideoContext)
  getConversationParticipant(conversation)

  const showDeviceSelectionScreen = () => {
    hideDomElements()
    props.showDeviceSelectionScreen(true)
    functionalCTADLPush('click', 'start cta', 'uaevent')
  }



  useEffect(() => {
    window.addEventListener('beforeunload', disconnectConversation)
    window.addEventListener('unload', disconnectConversation)
  	updateGADataForClient(props.serviceName)
    setAppointmentDate(props.appointmentDate)
    setAppointmentTime(props.appointmentTime)
    setAppointmentType(props.serviceName)
    const waitingroomcontainer = select('.waitroom-container')
    if (waitingroomcontainer) {
      waitingroomcontainer.insertAdjacentHTML('afterend',
        ` <div class="waitingroom-footer">
            <h2 class="waitingroom-footer-header heading"> ${waitingRoomFooterHeading}</h2>
            <div>
                <p>${waitingRoomFooterP1}</p>
                <p>${waitingRoomFooterP2}</p>
                <p>${waitingRoomFooterP3}</p>
                <p>${waitingRoomFooterP4}</p>
            </div>
          </div>`)
      const appointmentMessage = select('.waitroom-appointmentMessage')
      appointmentMessage ? appointmentMessage.focus() : ''
    }
    return () => {
      window.removeEventListener('unload', disconnectConversation)
      window.removeEventListener('beforeunload', disconnectConversation)
      const waitingRoomFooter = select('.waitingroom-footer')
      if (waitingRoomFooter) { waitingRoomFooter.remove() }
    }
  }, [])


  const handleMessageAdded = async (message) => {
    if (message.attributes.type === "notification") {
      const [action, participantId] = message.body.split('$')
      const myConvDetails = await getMyDetails()
      if(action === "accept" && participantId === myConvDetails.sid) {
        setHasBAAccepted(true)
        functionalCTADLPush('display', 'start cta', 'nievent')
      }
    }
  }

  useEffect(() => {
    if (conversation) {
      conversation && conversation.on('messageAdded', handleMessageAdded)
      conversation.getMessages().then(messageList => {
        messageList.items.map(message => {
          handleMessageAdded(message)
        })
        
      })

    }
  }, [conversation])


  return (
    <React.Fragment>
      <div className="waitroom-container">
        <div className="context has-text-centered">
          <div className="waitroom-header">
            <p role="status" tabIndex="-1" className="waitroom-appointmentMessage"> {!hasBAAccepted  ? waitingRoomHeader : waitingRoomHeaderAccepted}</p> 
            {hasBAAccepted  && <Button
              buttonClass="is-primary"
              id="check-media-access-button"
              onClick={showDeviceSelectionScreen}
              buttonText={startCTA}
              spanClass="button__wrapper" />
            }
          </div>
          <div className="twilio-main-box cl-waitingroom-mobile">
            <h1 className="heading cl-page-heading is-white">{heading}</h1>
            <div className=" client-room subhead is-white">
              <h2 className="beauty-text booking-detail-head">{appointmentType}</h2>
              <div className="meeting-text">
                <p className="gestures-message">{waitingRoomText}</p>
              </div>
            </div>
            <div className="details-container appointment-schedule">
              <h2 className="appointment-date">{appointmentDate}</h2>
              <p className="appointment-time">{appointmentTime}</p>
            </div>
          </div>
        </div>
      </div>
    </React.Fragment>
  )

}


export default WaitingRoom