import React, { useEffect, useState, createContext, Suspense } from 'react'
import { select } from 'lib/selectors'
import WaitingRoom from './waitingroom'
import DeviceSelection from './deviceSelection'
import ChanelTwilioConsultation from './chanelTwilioConsultation'
import * as xhr from '../../../modules/fetch'
import { ecommerceAnalyticsDLPush } from '../Utils/analytics'
import Loader from 'ui/widgets/loader'

export const VideoContext = createContext({
  room: null,
  setRoom: () => {},
  hasBAAccepted: false,
  setHasBAAccepted: () => {},
  token: null,
  conversation: null,
  setConversation: () => {},
  isChatEnabled: false,
  setIsChatEnabled: () => {},
  addedProductList: [],
  setAddedProductList: () => {},
  notificationModalIsOpen: false,
  setNotificationModalIsOpen: () => {},
  showAcceptButton: true, 
  setShowAcceptButton: () => {},
  clientList: true, 
  setClientList: () => {},
  userIconText: null,
  setUserIconText: () => {},
  waitCount: 0,
  setWaitCount: () => {},
  connectedCount: 0,
  setConnectedCount: () => {}

})

const ChanelTwilioVideo = (props) =>
{
  const { currentTimestamp, twilioToken } = props
  const { isBA, loading, participantWaiting } = jsConfigSecond.twilio
  const [showDeviceSelection, setShowDeviceSelection] = useState(isBA)
  const [reJoined, setReJoined] = useState(false)
  const [room, setRoom] = useState(props.room ? props.room : null)
  const [hasBAAccepted, setHasBAAccepted] = useState(false)
  const [isChatEnabled, setIsChatEnabled] = useState(false)
  const [viewThankYouPage, setViewThankYouPage] = useState(false)
  const [sharedProduct, setSharedProduct] = useState([])
  const [conversation, setConversation] = useState(props.conversation ? props.conversation : '')
  const [token, setToken] = useState(twilioToken)
  const [addedProductList, setAddedProductList] = useState([])
  const [notificationModalIsOpen, setNotificationModalIsOpen] = useState(false)
  const [showAcceptButton, setShowAcceptButton] = useState(true)
  const [clientList, setClientList] = useState([])
  const [waitCount, setWaitCount] = useState(0)
  const [connectedCount, setConnectedCount] = useState(0)
  const [userIconText, setUserIconText] = useState('0 ' + participantWaiting)

  const ThankYouPage = React.lazy(() => import('./thankYouPage'));


  const showDeviceSelectionScreen = (value) =>
  {
    setShowDeviceSelection(value)
  }

  const closeClicked = (value) =>
  {
    setShowDeviceSelection(!value)
  }

  const reJoinedClicked = (value) =>
  {
    setShowDeviceSelection(false)
    setReJoined(value)
  }

  const productShared = (productList) =>
  {
    let uniqueProductList = Object.values(productList.reduce((acc, cur) => Object.assign(acc, { [cur.code]: cur }), {}))
    setSharedProduct(uniqueProductList)
  }

  const onLeaveConfirmed = () =>
  {
    setViewThankYouPage(true)
    const mainWrapper = select('.twilio-content-container')
    const loader = new Loader(mainWrapper, {
      button: false,
      delay: 0,
    })
    loader.init()
    loader.create()
    setReJoined(false)
    const requestdata = {
      'room-id': jsConfigSecond.twilio.roomId,
    }
    xhr.get(jsConfigSecond.twilio.fetchOCProductSharedList, { data: requestdata })
      .then((res) =>
      {
        loader && loader.destroy()
        const prodList = Array.from(res.products)
        productShared(prodList)
        ecommerceAnalyticsDLPush(prodList)

      })
  }

  const videoContextValue = {
    room: room,
    setRoom: setRoom,
    hasBAAccepted: hasBAAccepted,
    setHasBAAccepted: setHasBAAccepted,
    token: token,
    conversation: conversation,
    setConversation: setConversation,
    isChatEnabled: isChatEnabled,
    setIsChatEnabled: setIsChatEnabled,
    addedProductList: addedProductList,
    setAddedProductList: setAddedProductList,
    notificationModalIsOpen: notificationModalIsOpen,
    setNotificationModalIsOpen: setNotificationModalIsOpen,
    showAcceptButton: showAcceptButton, 
    setShowAcceptButton: setShowAcceptButton,
    clientList: clientList,
    setClientList: setClientList,
    userIconText: userIconText,
    setUserIconText: setUserIconText,
    waitCount: waitCount,
    setWaitCount: setWaitCount,
    connectedCount: connectedCount,
    setConnectedCount: setConnectedCount



  }

  useEffect(() =>
  {
    twilioToken && twilioToken !== "" && setToken(twilioToken)
  }, [twilioToken])

  useEffect(() =>
  {
    if (isBA) {
      setShowDeviceSelection(true)
    }
  }, [currentTimestamp])

  return (
    <VideoContext.Provider value={videoContextValue} >
      {!isBA && !showDeviceSelection && !reJoined && !viewThankYouPage && <WaitingRoom {...props} showDeviceSelectionScreen={showDeviceSelectionScreen} />}
      {showDeviceSelection && <DeviceSelection closeClicked={closeClicked} reJoinedClicked={reJoinedClicked} />}
      {reJoined && <ChanelTwilioConsultation onLeaveConfirmed={onLeaveConfirmed} />}
      {viewThankYouPage && <Suspense fallback={<div>{loading}</div>}><ThankYouPage sharedProduct={sharedProduct} /></Suspense>}
    </VideoContext.Provider>
  );
}


export default ChanelTwilioVideo
