import React, { useContext, useEffect, useState } from 'react'
import ParticipantListElement from './participantListElement'
import MasterClassClientList from './master-class/masterClassClientList'
import Button from './button'
import { select, getCollection } from 'lib/selectors'
import bind from 'delegate'
import Modal from 'react-modal'
import { VideoContext } from './chanelTwilioVideo'
import { isMobileScreen} from 'lib/helpers'
import CloseButton from './closeButton'
import Icon from './Icon'
import { a11yNotificationText } from '../Utils/helper'

function UserIcon(props) {

  const { isChatEnabled, notificationModalIsOpen, clientList , userIconText, setUserIconText, waitCount, setWaitCount, setConnectedCount} = useContext(VideoContext)
  const { notifAriaLabelText, isMasterClass, noClientWaiting, closeNotificationPopup } = jsConfigSecond.twilio
  const [isMobile, setIsMobile] = useState(isMobileScreen())

  const onEscapeClicked = (event) => {
    const { key } = event
    if (key === "Escape") {
      props.closeNotificationModal()
    }
    // to manage aria-live attribute of user icon on keydown
    onfocusout()
  }

  const resizeHandler = () => {
    setIsMobile(isMobileScreen())
  }
  const afterOpenModal = () => {
    window.addEventListener('resize', resizeHandler)
    if (isChatEnabled && !isMobile) {
      const pixelToRem = 16
      const notiContent = select(".notification-content")
      const chatContainer = select(".chat__container")
      var userIconStyle = window.getComputedStyle(select(".user-icon"))
      var rightProperty = userIconStyle.getPropertyValue('right').slice(0,2)
      var userNotificationRight = (chatContainer.clientWidth/pixelToRem) + (parseInt(rightProperty)/pixelToRem)
      notiContent.classList.add("notification-align")
      select(".notification-align").style.right = userNotificationRight +"rem"
    }
  }

  const onfocusout = () => {
    const userIcon = getCollection('.notification-icon')
    userIcon && userIcon.forEach(item => {
     
      if (item !== document.activeElement && item.hasAttribute('aria-live') && item.getAttribute('aria-live') === 'off') {
        item.setAttribute('aria-live', 'assertive')
      }
    })
  }

  const participantAttrUpdate = (data) => {
    if(data.updateReasons.includes('attributes')) {
      console.log("Participant attributes updated!")
      setUserIconText(a11yNotificationText(clientList))
      setWaitCount(clientList.filter(client => !client.attributes.accepted).length)
      setConnectedCount(clientList.filter(client => client.attributes.connected).length)
    }
  }

  useEffect(() => {
    bind(document.body, '.notification-icon', "focusout", onfocusout)
    window.addEventListener('keydown', onEscapeClicked)
    return () =>
    {
      window.removeEventListener('keydown', onEscapeClicked)
    }
  }, [])

  useEffect(() => {
    clientList.length && clientList[clientList.length-1].on('updated', (data) => participantAttrUpdate(data))
    setUserIconText(a11yNotificationText(clientList))

    return () => {
      clientList.map(client => client.off('updated', (data) => participantAttrUpdate(data)))
    }
    
  }, [clientList]);


  return (
    <React.Fragment>
      <Button
        onClick={() => props.openNotificationModal()}
        spanClass="is-sr-only"
        buttonText={userIconText}
        buttonClass={`notification-icon ${waitCount > 0  ? ' user-active cursor' : ''}`}
        iconName="account"
        iconSize="large"
        ariaLive="assertive"
      />
      <Modal isOpen={notificationModalIsOpen}
        onRequestClose={() => props.closeNotificationModal()}
        onAfterOpen={afterOpenModal}
        appElement={select('.twilio-content-container')}
        contentLabel={notifAriaLabelText}
        portalClassName="notification-portal"
        overlayClassName="notification-overlay"
        className="notification-content">
        <div className={`participant-list ${isMasterClass ? 'master-class' : ''}`} role="document">
          {!isMasterClass &&
            clientList.length > 0 && <ul>
              {clientList.map((participant, index) =>
                <ParticipantListElement participant={participant} onClick={() => props.onAcceptClicked(participant)} key={'participant-' + index} />
              )}
            </ul>
          }
          {isMasterClass && clientList.length > 0 && 
          <React.Fragment>
              <CloseButton onClick={() => props.closeNotificationModal()} buttonText={closeNotificationPopup} iconSize='large' />
              <MasterClassClientList onClick={(participant) => props.onAcceptClicked(participant)} />
           </React.Fragment>
          }
          {isMasterClass && clientList.length === 0 && 
          <React.Fragment>
              <CloseButton onClick={() => props.closeNotificationModal()} buttonText={closeNotificationPopup} iconSize='large' />
              <div className="paticipant-container no-participant" tabIndex = '-1'>
	            <Icon name={'account'} size={'large'} />
	            <span className="part-name">{noClientWaiting}</span>
              </div>
           </React.Fragment>
          }
        </div>
      </Modal>

    </React.Fragment>

  )
}

export default UserIcon