import React, { useState, useContext, useEffect } from "react";
import Button from "../button";
import { VideoContext } from '../chanelTwilioVideo'
import UserIcon from "../userIcon";
import { isMobileScreen } from 'lib/helpers'


function ChatHeader(props)
{
  const { returnBtnText, hideBtnText, titleText, isBA, backBtnAriaLabel, hideChatAriaLabel } = jsConfigSecond.twilio
  const { setIsChatEnabled } = useContext(VideoContext)
  const { totalParticipant,
    onAcceptClicked,
    closeNotificationModal,
    openNotificationModal } = props

  const [isMobile, seIsMobile] = useState(isMobileScreen())

  const resizeHandler = () =>
  {
    seIsMobile(isMobileScreen())
  }

  const chatWindowHandler = () =>
  {
    setIsChatEnabled(false)
  }

  useEffect(() =>
  {
    window.addEventListener('resize', resizeHandler)

    return () =>
    {
      window.removeEventListener('resize', resizeHandler)
    }
  }, [])

  return (
    <div className="header__container">
      <Button buttonClass="header__container_returnBtn" spanClass={!isMobile ? 'is-sr-only' : ''} iconName="back" onClick={chatWindowHandler} buttonText={returnBtnText} ariaLabel={backBtnAriaLabel}></Button>
      <Button buttonClass="header__container_hideBtn" onClick={chatWindowHandler} buttonText={hideBtnText} ariaLabel={hideChatAriaLabel} />
      <p role="heading" aria-level="1" className={`header__container_chatTitle ${isMobile ? 'is-sr-only' : ''}`}>{titleText}</p>
      {isBA && isMobile && <div className='consultation__header-right'>
        <div className='user-icon'>
          <UserIcon
            onAcceptClicked={onAcceptClicked}
            closeNotificationModal={closeNotificationModal}
            openNotificationModal={openNotificationModal}
          />
        </div>
      </div>
      }
    </div>
  )
}

export default ChatHeader
