import React, { useEffect, useState, useRef, useContext } from 'react'
import { select, getCollection } from 'lib/selectors'
import * as xhr from '../../../modules/fetch'
import Button from './button'
import Icon from './Icon'
import CatalogListElement from './catalogListElement'
import Modal from 'react-modal'
import { functionalCTADLPush, getImage } from '../Utils/helper'
import { focusElement } from 'lib/helpers'
import { ShareProductContext } from './chanelTwilioConsultation'
import { VideoContext } from './chanelTwilioVideo'

const CatalogSearch = (props) =>
{
  const { isMobileView, a11y, onAddToListClicked, handleSharedProduct } = props
  const {
    searchTabText,
    searchInputLabel,
    submitSearchText,
    submitSearchSRText,
    catalogFetchPro,
    catalogProCount,
    catalogNoPro,
    productInfoPopinHeading,
    productInfoPopinShareCtaText,
    productInfoPopinAddToListCtaText,
    closeText,
    shadeDropdownAriaLabel,
    isBA,
    productRef
  } = jsConfigSecond.twilio
  const [search, setSearch] = useState('')
  const [filteredProducts, setFilteredProducts] = useState([])
  const [loading, setLoading] = useState(false)
  const [selectedProduct, setSelectedProduct] = useState({})
  const [productInfoModalIsOpen, setProductInfoModalIsOpen] = useState(false)
  const [shadeDropdownToggle, setShadeDropdownToggle] = useState(false)
  const [variantProducts, setVariantProducts] = useState([])
  const [shadeOptions, setShadeOptions] = useState([])
  const defaultImg = `${config.theme}/assets/media/product_default.png`
  const closeButtonElm = useRef(null)
  const [activeIndex, setActiveIndex] = useState(0)
  const { connectedCount } = useContext(VideoContext)


  const setFocus = (length) =>
  {
    const noProdEl = select('.no-products')
    const level3Heading = select('.level-3-heading')
    if (length == 0) {
      noProdEl ? select('.no-products').focus() : ''
    } else {
      level3Heading.focus()
    }
  }

  const onShareProductClicked = (product) =>
  {
    setProductInfoModalIsOpen(false)
    handleSharedProduct(product)
  }

  const handleMoreProductInfo = (product) =>
  {
    setProductInfoModalIsOpen(true)
    setSelectedProduct(product)
    let variantProductInfo = filteredProducts.filter((item) => item.baseProduct === product.baseProduct)
    setVariantProducts(variantProductInfo)
    if (variantProductInfo && variantProductInfo.length > 0) {
      let shades = variantProductInfo.map((item) => item.shade)
      setShadeOptions(shades)
    }
    functionalCTADLPush('open_popin', 'search_page', product.name)
  }

  const handleShadeDropdown = () =>
  {
    setShadeDropdownToggle(!shadeDropdownToggle)

    !shadeDropdownToggle ? document.addEventListener('keydown', (e) => isOutOfDropdown(e)) : document.removeEventListener('keydown', (e) => isOutOfDropdown(e))
  }

  const isOutOfDropdown = (e) =>
  {
    e.keyCode == 9 && e.shiftKey && document.activeElement.classList.contains('selected__shade') ? setShadeDropdownToggle(false) : ''
    const selectedItem = document.activeElement.parentElement
    if (!shadeDropdownToggle && e.keyCode == 9 && selectedItem && selectedItem.parentElement && selectedItem.parentElement.children) {
      const selectedItemIndex = [...selectedItem.parentElement.children].indexOf(selectedItem)
      if (selectedItem && selectedItem.classList.contains('shade__item') && selectedItem.parentElement && selectedItem.parentElement.children) {
        const dropdownListItems = getCollection('.ul-shade-list li')
        if (selectedItemIndex === dropdownListItems.length - 1 && !e.shiftKey) {
          setShadeDropdownToggle(false)
        }
        if (e.shiftKey && selectedItemIndex === 0) {
          setShadeDropdownToggle(false)
          closeButtonElm && closeButtonElm.current.focus()
          e.preventDefault()
        }
      }
    } else if (e.key === 'Escape' || e.key === 'Esc' || e.keyCode === 27) {
      setShadeDropdownToggle(false)
      e.preventDefault()
    }
  }

  const closeShadeDropdown = (product) =>
  {
    setShadeDropdownToggle(false)
    let selectedProductVariant = variantProducts.filter((item) => item.code === product.code)
    setSelectedProduct(selectedProductVariant[0])
    focusElement(select('.modal__heading'))
  }

  const afterOpenModal = () =>
  {
    closeButtonElm.current.focus()
  }

  useEffect(() =>
  {
    let modalContent = select('.product-info-content')
    if (productInfoModalIsOpen) {
      if (shadeDropdownToggle) {
        let dropDownHeight = select('.ul-shade-list li').clientHeight
        modalContent.style.height = `${modalContent.clientHeight + dropDownHeight * 3}px`
      } else {
        modalContent.style.height = 'auto'
      }
    }
  }, [shadeDropdownToggle])

  useEffect(() =>
  {
    var searchInputLabel = select(".search__input__label")
    if (search !== '' && search.length > 0) {
      searchInputLabel.classList.add("is-sr-only")
      var searchResults = []
      if (search.length >= 3) {
        const proData = JSON.parse(localStorage.getItem('proData'))
        if (proData && proData !== '') {
          searchResults = proData.filter((product) => product.name && product.name.toLowerCase().includes(search.toLowerCase()))
          setFilteredProducts(searchResults)
          functionalCTADLPush('search results', 'type keywords', search, 'uaevent')
        }
      } else {
        setFilteredProducts('')
      }
    }else{
      searchInputLabel.classList.remove("is-sr-only")
      setFilteredProducts('')
    }
  }, [search])

  useEffect(() =>
  {
    setLoading(true)
    var proData = localStorage.getItem('proData')
    let isCatalogSearchApiHit = JSON.parse(localStorage.getItem('isCatalogSearchApiHit'))
    setFilteredProducts('')
    if (!proData && !isCatalogSearchApiHit) {
      localStorage.setItem('isCatalogSearchApiHit', 'true')
      xhr.get(jsConfigSecond.twilio.fetchProductCatalog + '?room-id='+jsConfigSecond.twilio.roomId, {
        fetchTimeout: 50000
       })

        .then((res) =>
        {
          proData = JSON.stringify(res)
          localStorage.setItem('proData', proData)
          setLoading(false)
        })
        .catch((err) =>
        {
          console.error(err)
        })
    } else {
      setLoading(false)
    }

    a11y ? select('.js-level2-heading').focus() : ''
    const btnMyListTab = select('button.my-list-tab')
    const btnSearchTab = select('button.search-tab')
    btnMyListTab && btnMyListTab.setAttribute('aria-live', 'polite')
    btnSearchTab && btnSearchTab.setAttribute('aria-current', true)
    return () =>
    {
      btnSearchTab.removeAttribute('aria-current')
    }
  }, [])

  if (loading) {
    return <p>{catalogFetchPro}</p>
  }

  return (
    <div className='catalog-search'>
      <div role='heading' aria-level='2' className='is-sr-only js-level2-heading' tabIndex='-1'>
        {searchTabText}
      </div>
      <div className='search__content'>
        <div className='search__bar'>
          <div className={`search__form ${search !== '' ? 'has-content' : ''}`} role='search'>
            {isMobileView && <Icon name='search' size='large' />}
            <div className={`label-input search__input has-icon ${search !== '' ? 'has-content' : ''}`}>
              <label htmlFor='catalogSearchInput' className='label-input__label search__input__label'>
                <span> {searchInputLabel} </span>
              </label>
              <input
                type='search'
                id='catalogSearchInput'
                required='required'
                aria-required='true'
                className='label-input__input search__input__field'
                title={searchInputLabel}
                onChange={(e) => setSearch(e.target.value)}
              />
            </div>
            <Button
              onClick={() => setFocus(filteredProducts.length)}
              type='submit'
              spanClass={isMobileView ? '' : 'is-sr-only'}
              buttonText={isMobileView ? submitSearchText : submitSearchSRText}
              buttonClass='search__form__submit'
              iconName={isMobileView ? '' : 'search'}
              iconSize={isMobileView ? '' : 'large'}
            />
            {isMobileView && <Icon name='next' styleModifier='search-next' size='xsmall' />}
          </div>
        </div>
      </div>
      {search !== '' && search.length > 3 && (
        <div className='search__result__container '>
          <div role='heading' aria-level='3' className='is-sr-only level-3-heading' aria-live='polite' aria-atomic='true' tabIndex='-1'>{`${filteredProducts.length} ${catalogProCount} ${search}`}</div>
          <div className='prod__list'>
            {filteredProducts.length == 0 && search.length > 2 && (
              <p tabIndex='-1' className='no-products'>
                {catalogNoPro}
              </p>
            )}
            {filteredProducts.length > 0 &&
              Array.from(filteredProducts).map((item, index) => <CatalogListElement handleMoreProductInfo={handleMoreProductInfo} item={item} isMobileView={isMobileView} key={'item-' + index} />)}
          </div>
        </div>
      )}

      <Modal
        isOpen={productInfoModalIsOpen}
        appElement={select('.twilio-content-container')}
        contentLabel={`${selectedProduct.name ? selectedProduct.name : ''} ${selectedProduct.description ? selectedProduct.description : ''}`}
        portalClassName='product-info-portal'
        overlayClassName='product-info-overlay'
        className='product-info-content'
        shouldCloseOnOverlayClick={true}
        onAfterOpen={afterOpenModal}
      >
        <div className='product-item__media'>
          <img src={`${selectedProduct.basic && selectedProduct.basic.images ? getImage(selectedProduct.basic.images) : defaultImg}`} alt='' />
        </div>

        <div className='product-item__content'>
          <button
            className='close__button'
            type='button'
            ref={closeButtonElm}
            onClick={() =>
            {
              setProductInfoModalIsOpen(false)
              setShadeDropdownToggle(false)
            }}
          >
            <Icon name={'close'} size={'large'} />
            <span className='is-sr-only'>{`${closeText} ${selectedProduct.name ? selectedProduct.name : ''} ${selectedProduct.description ? selectedProduct.description : ''}`}</span>
          </button>
          <div role='heading' aria-level='1' className='modal__heading'>
            <span role='presentation' className='prod-item__title heading'>
              {selectedProduct.name}
            </span>
            <span role='presentation' className='prod-item__description'>
              {selectedProduct.description}
            </span>
            <span className='is-sr-only'>{`${productRef}${selectedProduct.code}`}</span>
          </div>

          {selectedProduct && selectedProduct.shade && Object.keys(selectedProduct.shade).length > 0 && shadeOptions && shadeOptions.length > 1 && (
            <div className='shade__selector'>
              <div className='product-details__option'>
                <button
                  className='selected__shade'
                  type='button'
                  onClick={handleShadeDropdown}
                  aria-label={`${selectedProduct.shade.shade} - ${shadeDropdownAriaLabel}`}
                  aria-expanded={shadeDropdownToggle ? 'true' : 'false'}
                >
                  <span className='button__wrapper'>
                    <img src={selectedProduct.shade.url} alt=''></img>
                    <span>{selectedProduct.shade.shade}</span>
                    <Icon styleModifier={shadeDropdownToggle ? 'reverse-arrow' : ''} name={'down'} size={'small'} />
                  </span>
                </button>

                {selectedProduct.shade && selectedProduct.shade.shade && (
                  <div className={`variant__list ${!shadeDropdownToggle ? 'is-hidden' : ''}`}>
                    <ul className='ul-shade-list' data-count={shadeOptions.length}>
                      {shadeOptions &&
                        shadeOptions.length > 0 &&
                        variantProducts.map((item, index) => (
                          Object.keys(item.shade).length > 0 && (
                            <li
                              key={index}
                              className='shade__item'
                              onClick={() =>
                              {
                                closeShadeDropdown(item)
                                setActiveIndex(index)
                              }}>
                              <button type='button' aria-pressed={activeIndex === index}>
                                <span className='button__wrapper'>
                                  <img src={item.shade.url} alt='' />
                                  <span>{item.shade.shade}</span>
                                </span>
                              </button>
                            </li>
                          )
                        ))}
                    </ul>
                  </div>
                )}
              </div>
            </div>
          )}

          {selectedProduct && selectedProduct.shade && Object.keys(selectedProduct.shade).length > 1 && shadeOptions && shadeOptions.length == 1 && (
            <div className='product-details__option'>
              <span className='button__wrapper'>
                <img src={selectedProduct.shade.url} alt=''></img>
                <p className='shade__name'>{selectedProduct.shade.shade}</p>
              </span>
            </div>
          )}
        </div>

        <div className='product-item-cta'>
          {isBA && connectedCount > 0 && (
            <Button
              buttonText={productInfoPopinShareCtaText}
              buttonClass='is-primary is-block'
              onClick={() => onShareProductClicked(selectedProduct)}
              ariaLabel={`${productInfoPopinShareCtaText} - ${selectedProduct && selectedProduct.name} ${selectedProduct && selectedProduct.description} ${(selectedProduct.shade && selectedProduct.shade !== {} && selectedProduct.shade.shade) || ''
                }`}
            />
          )}
          <Button
            buttonText={productInfoPopinAddToListCtaText}
            buttonClass='is-secondary is-block'
            onClick={() =>
            {
              setProductInfoModalIsOpen(false)
              onAddToListClicked(selectedProduct)
            }}
            ariaLabel={`${productInfoPopinAddToListCtaText} - ${selectedProduct && selectedProduct.name} ${selectedProduct && selectedProduct.description} ${(selectedProduct.shade && selectedProduct.shade !== {} && selectedProduct.shade.shade) || ''
              }`}
          />
        </div>
      </Modal>
    </div>
  )
}

export default CatalogSearch
