import React, { useCallback, useEffect, useState } from 'react'
import Icon from './Icon'

const Dropdown = (props) => {
  const {dropdownLabel, disabled, onSelect, dropdownIcon, dropdownTitle} = props
  const [dropdownValue, setDropdownValue] = useState(props.dropdownOptions ? props.dropdownOptions[0] : '')
  const [dropdownOptions, setDropdownOptions] = useState(props.dropdownOptions)
  var selectedDevice

  useEffect(() => {
    setDropdownOptions(props.dropdownOptions)
    setDropdownValue('')
  }, [props.dropdownOptions])

  useEffect(() => {
    selectedDevice = dropdownOptions && dropdownOptions.filter(val => val.label === dropdownValue)[0]
    onSelect(selectedDevice)
  }, [dropdownValue])

  return (
    <div className={`selectbox is-simple is-ready has-content ${disabled ? "dropdown-disabled" : ""}`}>
      <select title= {`${dropdownValue ? dropdownValue : dropdownLabel  + " " + (props.dropdownOptions ? props.dropdownOptions.length : "")} ${dropdownTitle}`}
          className="selectbox__input" 
          disabled={disabled} 
          name={dropdownLabel} 
          value={dropdownValue} 
          onChange={e=>setDropdownValue(e.target.value)} 
          required 
          lang={window.config.currentLanguage}>
        {
        dropdownOptions &&
          dropdownOptions.map(
            (element, index) => (
              <option key={index} value={element.label}>
                {element.label}
              </option>
            )
          )
          }
      </select>
      <span className="selectbox__wrapper" aria-hidden="true">
        {dropdownIcon && <span className="selectbox__icons"><Icon name={dropdownIcon} size={"large"} /></span>}
        <span className="selectbox__label">{dropdownValue ? dropdownValue : dropdownLabel + " " + (props.dropdownOptions ? props.dropdownOptions.length : "")} </span>
      </span>
      <Icon name={"down"} size={"small"} />
    </div>
  );
}


export default Dropdown
