import React from "react"
import Modal from "react-modal"
import { select } from "lib/selectors"
import Button from "./button"
import CloseButton from "./closeButton"

function ConfirmationModal(props) {
    const { handleDeleteProduct, showRemoveProdModel, closeRemoveProdModel, item } = props
    const { deletePopinHeading, deleteProductText, conButton, canButton, confirmBtn, cancelBtn, closeText } = jsConfigSecond.twilio
    const ariaLabelMainHeading = `${item.name ? item.name : ""} ${item.description ? item.description : ""} ${item.shade ? item.shade : ""} ${item.code ? item.code : ""}`

    return (
        <Modal
            isOpen={showRemoveProdModel}
            appElement={select(".twilio-content-container")}
            contentLabel={deletePopinHeading}
            onRequestClose={closeRemoveProdModel}
            portalClassName="delete-product"
            overlayClassName="delete-product__overlay"
            className="delete-product__content"
            shouldCloseOnOverlayClick={false}
            id={item.code}
        >

            <div role="document">
                <CloseButton 
                    buttonClass="delete-product__close_button"
                    buttonText={`${closeText} - ${deletePopinHeading}`}
                    onClick={closeRemoveProdModel}
                    iconSize="large"
                />
                
                <div role="heading" aria-level="1" className="delete-product__title heading" id={`prod-${item.code}`} tabIndex="-1">
                    {deletePopinHeading}
                </div>
                <p className="delete-product__description">{deleteProductText}</p>
                <div className="context">

                    <Button
                        buttonClass="is-primary is-block confirm_prod"
                        buttonText={conButton}
                        ariaLabel={`${confirmBtn} : ${ariaLabelMainHeading}`}
                        ariaDescribedby={item.code}
                        onClick={handleDeleteProduct}
                    />

                    <Button buttonText={canButton} 
                        buttonClass="is-secondary is-block cancel_prod" 
                        ariaLabel={cancelBtn} 
                        onClick={closeRemoveProdModel} 
                    />

                </div>
            </div>
        </Modal>
    )
}

export default ConfirmationModal
