import React from "react"
import Modal from "react-modal"
import { select } from "lib/selectors"
import Button from "../button"
import CloseButton from "../closeButton"

function RemoveConfirmationModal(props) {
  const { name, showRemoveModel, closeRemoveModel, handleRemoveParticipant} = props 
  const { conButton, canButton, removeParticipantLabel, removeParticipantText } = jsConfigSecond.twilio
  const removeDescriptionText = removeParticipantText.replace('{0}', name)

  return (
    <Modal
      isOpen={showRemoveModel}
      onRequestClose={closeRemoveModel}
      appElement={select('.twilio-content-container')}
      contentLabel={removeParticipantLabel}
      portalClassName='remove-portal'
      overlayClassName='remove-overlay'
      className='remove-content'
      shouldCloseOnOverlayClick={true}
    >
      <div role="document">
        <CloseButton 
          buttonClass="remove-close_button"
          buttonText={`Close ${removeParticipantLabel}`}
          onClick={closeRemoveModel}
          iconSize="large"
        />
        <p className='heading is-6 remove-heading' role='heading' aria-level='1'> {removeParticipantLabel} ?</p>
        <p id="remove-desc">{removeDescriptionText}</p>
        <div className='context'>
          <Button
            buttonClass="is-primary is-block"
            buttonText={conButton}
            ariaDescribedby="remove-desc"
            onClick={handleRemoveParticipant}
          />

          <Button buttonText={canButton}
            buttonClass="is-secondary is-block"
            ariaDescribedby="remove-desc"
            onClick={closeRemoveModel}
          />
        </div>
      </div>
    </Modal>
  );
}

export default RemoveConfirmationModal;