import React, { useState, useContext } from "react"
import Button from "../button"
import { VideoContext } from '../chanelTwilioVideo'
import { functionalCTADLPush } from '../../Utils/helper'
import { select } from 'lib/selectors'
import { getOS } from "lib/helpers"

function ChatFooter()
{
  const { sendBtnText, isBA, chatMsgInputTitle, sendButtonAriaLabel } = jsConfigSecond.twilio
  //react states
  const [isMsgSend, setIsMsgSend] = useState(false)
  const [inputMessage, setInputMessage] = useState('')

  //react contexts
  const { conversation } = useContext(VideoContext)

  //react functions
  const chatMsgHandler = () =>
  {
    if (inputMessage == "") return false
    setIsMsgSend(!isMsgSend)
    if (inputMessage) {
      inputMessage != "" && conversation.sendMessage(inputMessage, { "type": "text", "isBA": isBA })
    }
    setInputMessage("")
    select('.footer__container_input').focus()
    functionalCTADLPush('chat', 'message sent', '')
  }

  return (
    <div className="footer__container">
      <form onSubmit={(e) => e.preventDefault()}>
        <input
          id="chat-message-input"
          type="text"
          value={inputMessage}
          aria-required="true"
          className="footer__container_input"
          title={chatMsgInputTitle}
          placeholder={chatMsgInputTitle}
          onChange={(e) => setInputMessage(e.target.value)} />
        <Button
          type="submit"
          buttonClass="footer__container_sendMsg"
          onClick={chatMsgHandler}
          buttonText={sendBtnText}
          iconName="next"
          iconSize="small"
          ariaLabel={sendButtonAriaLabel}
          ariaDisabled={inputMessage === "" && "true"}
        />
      {getOS() === 'Mac OS' && <div id="msg-ally" role="log" aria-live="polite" className="is-sr-only"></div>}
      </form>
    </div>
  )
}

export default ChatFooter