import React, { useEffect, useState, useContext } from 'react'
import Button from './button'
import { getImage } from '../Utils/helper'
import { VideoContext } from './chanelTwilioVideo'

const CatalogListElement = (props) =>
{
    const { handleMoreProductInfo, item, isMobileView, isListTab = false, handleSharedProduct, deleteProductClicked } = props
    const { actionText, srActionText, productInfoPopinShareCtaText, productInfoDeleteCtaText } = jsConfigSecond.twilio
    const defaultImg = `${config.theme}/assets/media/product_default.png`
    const { connectedCount } = useContext(VideoContext)
    const productImages = item.images || (item.basic && item.basic.images);

    return (
        <div className={`prod__list-item ${isListTab ? 'prod__list-item-list' : ''}`} id={`prod-${item.code}`} key={item.code}>
            <div className='prod-item__media'>
                {(productImages && <img src={`${getImage(productImages)}`} alt='' />) || <img src={defaultImg} alt='' />}
            </div>
            <div className='prod-item__content' id={item.code} >
                <p role='heading' aria-level='3'>
                    {item.name && (
                        <span role='presentation' className='prod-item__title heading'>
                            {item.name.toLowerCase()}
                        </span>
                    )}
                    {item.description && (
                        <span role='presentation' className='prod-item__description'>
                            {item.description.toLowerCase()}
                        </span>
                    )}
                    {isListTab && item.selectedVariantData && (
                        <span role='presentation' className='button__wrapper'>
                            {item.selectedVariantData.shadeAsset && item.selectedVariantData.shadeAsset.url && <img src={item.selectedVariantData.shadeAsset.url} alt=''></img>}
                            {item.selectedVariantData.shade && <p className='shade__name'>{item.selectedVariantData.shade}</p>}
                        </span>
                    )}
                    {isListTab && item.shade && (
                        <span role='presentation' className='button__wrapper'>
                            {item.shade.url && <img src={item.shade.url} alt=''></img>}
                            {item.shade.shade && <p className='shade__name'>{item.shade.shade}</p>}
                        </span>
                    )}
                </p>
                <p className='is-sr-only'>{item.code}</p>
            </div>
            <div className='prod-item__action'>
                {!isListTab && (
                    <Button
                        onClick={() => handleMoreProductInfo(item)}
                        buttonText={isMobileView ? srActionText : <span>{actionText}</span>}
                        buttonClass={isMobileView ? '' : 'is-tertiary'}
                        spanClass={isMobileView ? 'is-sr-only' : ''}
                        iconName={isMobileView ? 'close' : ''}
                        iconSize={isMobileView ? 'small' : ''}
                        ariaDescribedby={item.code}
                    />
                )}
                {isListTab && connectedCount > 0 && <Button onClick={() => handleSharedProduct(item)} buttonText={<span>{productInfoPopinShareCtaText}</span>} buttonClass='is-tertiary' ariaDescribedby={item.code} />}

                {isListTab && <Button onClick={() => deleteProductClicked(item)} buttonText={<span>{productInfoDeleteCtaText}</span>} buttonClass='is-tertiary' ariaDescribedby={item.code} />}
            </div>
        </div>
    )
}

export default CatalogListElement
