const headerWrapper = select('.js-header')
const skipLinks = select('.skip-links')
const footerWrapper = select('.js-footer')
const heroWrapper = select('.hero__container')
const hero = select('.hero')
const defaultImg = `${config.theme}/assets/media/product_default.png`
import scriptjs from 'scriptjs'
const { conversationSdk, cartMsgSuccess, cartMsgFail, fetchOCAddToBag, sharedProductA11yText, participantJoined, participantWaiting, isBA } = jsConfigSecond.twilio
let disconnectInvoked = false
var convParticipant = null
var myConversationPromise
// Follow accessibility specifications to add visibility hidden style over the document
function hideDomElements()
{
  skipLinks.classList.add('is-hidden')
  headerWrapper.classList.add('is-hidden')
  hero.classList.add('is-hidden')
  heroWrapper.classList.add('is-hidden')
  footerWrapper.classList.add('is-hidden')
}

// Follow accessibility specifications to remove visibility hidden style over the document
function showDomElements(showHeroWrapper)
{
  skipLinks.classList.remove('is-hidden')
  headerWrapper.classList.remove('is-hidden')
  footerWrapper.classList.remove('is-hidden')
  showHeroWrapper && heroWrapper.classList.remove("is-hidden")
  hero.classList.remove('is-hidden')
}

const functionalCTADLPush = (action = 'click', type, event = 'uaevent') =>
{
  dataLayer.push({
    'eventCategory': 'online consultation',
    'eventAction': action,
    'eventLabel': type,
    'event': event  // associated event
  })
}

const getFormattedMsgTime = (date) =>
  {
    let hours = date.getHours();
    let minutes = date.getMinutes();
    let ampm = hours >= 12 ? 'PM' : 'AM';
    hours = hours % 12;
    hours = hours ? hours : 12;
    minutes = minutes.toString().padStart(2, '0');
    let strTime = hours + ':' + minutes + ' ' + ampm;
    return strTime;
  }

const updateGADataForClient = (serviceName) =>
{
  const dlObj = {
    event: 'virtualpageview',
    newsletter_optin: 'newsletter non opt in',
    locale: dataLayer[0].locale,
    region_chanel: dataLayer[0].region_chanel,
    logged: 'not logged',
    division: 'fnb',
    page_type: 'online consultation - landing',
    userId: '', //to be kept blank
    divisionFlag: 'fnb',
    category: 'online consultation',
    sub_category_level1: 'landing',
    sub_category_level2: serviceName,
    page_name: dataLayer[0].page_name
  }
  dataLayer.push(dlObj)
}

const getImage = (images) =>
{
  let imageUrl = ''
  let result = ''
  result = images.filter((x) => x.assetType === 'PACKSHOT_DEFAULT')
  if (result && result != '') {
    imageUrl = result[0].url
  } else {
    result = images.filter((x) => x.assetType === 'BASIC_TEXTURE')
    result && result != '' ? (imageUrl = result[0].url) : (imageUrl = defaultImg)
  }
  return imageUrl
}

function loadTwilioSdk(name, sdkUrl, windowVar)
{
  const sdkPromise = new Promise(resolve => scriptjs(sdkUrl, () =>
  {
    resolve(windowVar)
    if (name === 'video') {
      document.body.classList.add('online-consultation')
      console.log("Status of Twilio Video support: ", Twilio.Video && Twilio.Video.isSupported)
    }
  }))
  return sdkPromise
}

function activateChat(token, roomName)
{

  const conversationPromise = new Promise(resolve =>
  {

    loadTwilioSdk('conversation', conversationSdk, 'window.conversationSdk').then(() =>
    {
      console.log("conversation sdk loaded");
      let clientOptions = { logLevel: 'debug' }
      Twilio.Conversations.Client.create(token)
        .then(client =>
        {
          window.chatClient = client
          client.on("connectionStateChanged", state => {
            if (state === 'connected') {
              client.getConversationByUniqueName(roomName) // update name to room.sid
              .then(newConversation => {
               
                  window.chatConversation = newConversation
               
                  resolve(newConversation)
              })
            }
          })

        })
    })
  })
  return conversationPromise
}

const addToBag = (randNum, prod, imgAltText) =>
{
  const requestdata = {
    'productCodePost': prod.code,
  }

  var msg = ""
  xhr.post(fetchOCAddToBag, { data: requestdata })
    .then((res) =>
    {
      res === true ? msg = cartMsgSuccess : msg = cartMsgFail
      showCartMsg(msg, prod.code, randNum, imgAltText)
      if (res) {
        const addToBagData = []
        addToBagData.push(prod)
        ecommerceAnalyticsDLPush(addToBagData, 'addToCart')
      }
    })
    .catch(console.error)
}

const showCartMsg = (msg, pId, randNum, imgAltText) =>
{
  const contentWrapper = getCollection('[data-id ="' + randNum + '-' + pId + '"]')
  contentWrapper.forEach((item) => {
    item.setAttribute("hidden", "true")
  })



  const elements = getCollection('[data-id-wrapper ="' + randNum + '-' + pId + '"]')
  elements.forEach(element =>
  {
    const imgWrapper = element.previousElementSibling
    if(imgWrapper && imgWrapper.classList.contains('product-item__media')) {
      const imgEl = select('img', imgWrapper)
      imgEl && imgEl.setAttribute('alt', imgAltText)
    }
    const msgEl = select('p.cart-msg', element)
    if (!msgEl) {
      var tag = document.createElement('p')
      tag.innerHTML = msg
      tag.classList.add('cart-msg')
      element ? element.appendChild(tag) : ''
      tag.setAttribute('tabindex', '-1')
      tag.focus()
    }
  })
}

function get_time_diff(date1, date2 = new Date().getTime()) {
  date1 = new Date(date1).getTime()
  const milisec_diff = date2 - date1
  console.log(milisec_diff)
  return milisec_diff
}
const executeA11y = (msgTime) => {
  const lastChatEl = select('.chat_Message:last-child')
  if ( (get_time_diff(msgTime) < 500) && lastChatEl && lastChatEl.classList.contains('share-product__content')) {
    const contentCont = select('.product-item__content', lastChatEl)
    const tag = document.createElement('p')
    tag.innerHTML = sharedProductA11yText
    tag.classList.add('is-sr-only')
    contentCont.appendChild(tag)
    setTimeout(() => {
      tag.parentNode.removeChild(tag);
    }, 3000)
  }
}

const disconnectConversation = () =>{
  console.log("-----------Disconnect conversation invoked.-------------",disconnectInvoked, chatConversation.status, chatClient.connectionState);
  if (!disconnectInvoked && window.chatConversation) {
    disconnectInvoked = true
    window.chatConversation.leave().then(() => {
      console.log("==== conversation.leave done ===");
      window.chatClient && window.chatClient.shutdown()
      console.log("==== chatClient.shutdown done ===");
    })
  }
}

const onOpenModal = () => {
  const el = select('.twilio-content-container')
  const shareProduct = select('.share-product')
  var ariaHiddenItemsStore = []
  const ariaHiddenItems = Array.from(document.body.children)
      .reduce((array, childEl) => {
        if (childEl.tagName !== 'SCRIPT'
             && childEl !== el && !childEl.classList.contains('twilio-content-container')) {
           array.push(childEl)
         }
          return array
        }, [])

    if (el && el.hasAttribute('aria-hidden')) {
      el.removeAttribute('aria-hidden')
    }
    ariaHiddenItems.forEach(ariaHiddenItem => {
      ariaHiddenItemsStore.push({
        ariaStateStore: ariaHiddenItem.getAttribute('aria-hidden')
      })
      ariaHiddenItem.setAttribute('aria-hidden', true)
      shareProduct.removeAttribute('aria-hidden')
    })
   window.ariaHiddenItems = ariaHiddenItems
   window.ariaHiddenItemsStore = ariaHiddenItemsStore
  }

const onCloseModal = () => {
  const itemStore = window.ariaHiddenItems
  itemStore && itemStore.forEach((ariaHiddenItem, index) => {
    const storedValue = window.ariaHiddenItemsStore[index].ariaStateStore
      if (storedValue) {
        ariaHiddenItem.setAttribute('aria-hidden', storedValue)
      } else {
       ariaHiddenItem.removeAttribute('aria-hidden')
      }
    })
    window.ariaHiddenItems = ''
    window.ariaHiddenItemsStore = ''
  }

  const adjustContainerHeight = (containerClass) => {
    const selectedContainerEl = select(containerClass)
    selectedContainerEl && (selectedContainerEl.style.height = window.innerHeight + 'px'
    )
  }

  const checkParticipantStatus = (participant, attr) => {
    return participant.attributes[attr] ? true : false
  }

// To display name in the format firstname+ first letter of last name+'.'
  const displayName = (name) => {
    const nameArr = name.trim().split(" ")
    return `${nameArr[0]}${(nameArr.length) > 1 ? " " + nameArr[nameArr.length - 1][0] + "." : ""}`
  }

  
  const a11yNotificationText = (list) => {
    const a11yconnectedClients = list.filter(client => {
      return client.attributes.accepted == true
    })
    const waitingCount = list.length - a11yconnectedClients.length
    const connectedCount = a11yconnectedClients.length // Clients who have been accepted to consultation


    const waitingText = waitingCount > 0 ? waitingCount + " " + participantWaiting : ""
    const connectedText = connectedCount > 0 ? connectedCount + " " +  participantJoined : ""

    let notificationText = `${waitingText} ${waitingText && connectedText ? " and " : ""} ${connectedText}`
    notificationText = !notificationText.trim() ? `0 ${participantWaiting}` : notificationText

    return notificationText
  }

  // returns my participant object for the current conversation 
  const getMyDetails = async () => {
    !convParticipant && await myConversationPromise.then( response => convParticipant = response)
    return convParticipant
  }

  const getConversationParticipant = async (conversation) => {
    myConversationPromise = conversation.getParticipantByIdentity(chatClient.user.identity)
  }

  const removeParticipantAttrtibutes = (participant) => {
    delete participant.attributes.accepted
    delete participant.attributes.connected
  }

export
{
  hideDomElements,
  showDomElements,
  functionalCTADLPush,
  updateGADataForClient,
  getImage,
  loadTwilioSdk,
  activateChat,
  addToBag,
  executeA11y,
  disconnectConversation,
  onOpenModal,
  onCloseModal,
  getFormattedMsgTime,
  adjustContainerHeight,
  checkParticipantStatus,
  displayName,
  a11yNotificationText,
  getConversationParticipant,
  getMyDetails,
  removeParticipantAttrtibutes
}