import React, { Fragment, useContext, useState, useEffect } from 'react'
import { getImage, addToBag, executeA11y } from '../Utils/helper'
import { ChatContext, ShareProductContext } from './chanelTwilioConsultation'
import Button from './button'
import CloseButton from './closeButton'
import { select } from 'lib/selectors'
import WishListIcon from './wishListIcon'
import * as xhr from '../../../modules/fetch'

const ShareProductModalContent = (props) =>
{
  //react contexts
  const { shareProduct, setIsShareProductModalOpen, setIsShareProductModalClose, shareMessageId } = useContext(ShareProductContext)
  const { isChatEnabled } = useContext(ChatContext)
  //variables
  const {
    closeText,
    shareProductText,
    productRef,
    isBA,
    addToCartCTA,
    chatProductClientMessage,
    chatProdBAMsgClient,
    chatProdBAMsgClients,
    isProductInWishlist,
    availableAct
  } = jsConfigSecond.twilio
  let sharedProduct = props.shareProductMessage ? props.shareProductMessage : shareProduct
  let sharedMessageId = props.shareProductMessageId ? props.shareProductMessageId : shareMessageId
  const defaultImg = `${config.theme}/assets/media/product_default.png`
  const productImages = sharedProduct ? sharedProduct.images || (sharedProduct.basic && sharedProduct.basic.images) : null
  const shadeName = sharedProduct ? sharedProduct.selectedVariantData && sharedProduct.selectedVariantData.shade ? sharedProduct.selectedVariantData.shade : (sharedProduct.shade && sharedProduct.shade.shade) ? sharedProduct.shade.shade : '' : ''
  const ariaLabelMainHeading = sharedProduct ? `${sharedProduct.name ? sharedProduct.name : ''} ${shadeName} ${productRef} ${sharedProduct.code}` : ''
  const priceValue = sharedProduct ? `${sharedProduct.price && sharedProduct.price.formattedValue ? sharedProduct.price.formattedValue : sharedProduct.price ? sharedProduct.price : ''}` : null
  const [totalParticipantsState, setTotalParticipantsState] = useState(props.totalParticipant)
  const isBoutiqueApp = window.jsConfigSecond.isBoutiqueApp

  //react states
  const [isInWishList, setIsInWishList] = useState(false)
  const randNum = sharedMessageId
  const ariaLevelSharedProductHeading = props.isShareProductMessage ? '2' : '1'
  const uniqueId = props.msgTime ? `product-list-chat-${randNum}-${sharedProduct.code}`: `product-list-popin-${randNum}-${sharedProduct.code}`

  useEffect(() => {
    if(!isBA) {
      checkIsInWishlist()
      isChatEnabled && executeA11y(props.msgTime)
      var chatContainer = select('.chat__container')
      chatContainer && chatContainer.setAttribute("aria-hidden", true) 
    }
  }, [])

  useEffect(() =>
  {
    const shareBlock = select('.share-product')
    if (shareBlock) {
      let productcontentDetails = select('.product-item__content-details', shareBlock)
      let productcontent = select('.product-item__content', shareBlock)
      let productcontentMsg = select('p.cart-msg', productcontent)
      productcontentDetails.hasAttribute('hidden') && productcontentDetails.removeAttribute('hidden')
      productcontentMsg && productcontent.removeChild(productcontentMsg)
      const imgWrapper = productcontent.previousElementSibling
    if (imgWrapper && imgWrapper.classList.contains('product-item__media')) {
       const imgEl = select('img', imgWrapper)
     imgEl && imgEl.setAttribute('alt', '')
       }
    }
  }, [sharedProduct.code])

  useEffect(() =>
  {
    setTotalParticipantsState(props.totalParticipant)
  }, [props.totalParticipant])

  const checkIsInWishlist = () => {
    const requestdata = {
      productCode: sharedProduct.code
    }
    xhr.get(isProductInWishlist, { data: requestdata })
      .then((res) => {
        setIsInWishList(res)
      })
      .catch((err) => {
        console.error(err)
      })
  }
  const shareModalBtnClose = () => {
    setIsShareProductModalOpen(false)
    setIsShareProductModalClose(true)
    var chatContainer = select('.chat__container')
    chatContainer && chatContainer.hasAttribute('aria-hidden') && chatContainer.removeAttribute("aria-hidden")
  }

  return (
    <Fragment>
        {!props.isShareProductMessage && <CloseButton
          buttonClass='close__button'
          buttonText={`${closeText} - ${ariaLabelMainHeading}`}
          onClick={() => shareModalBtnClose() }
        />}
      {props.isShareProductMessage && <p className="is-sr-only">{isBA ? ( totalParticipantsState === 1 ? chatProdBAMsgClient : chatProdBAMsgClients) : chatProductClientMessage }</p>}
      {sharedProduct && productImages && <div className='product-item__media'>
        {(productImages && <img src={`${getImage(productImages)}`} alt='' />) || <img src={defaultImg} alt='' />}
      </div>}
      {sharedProduct && <div className='product-item__content' data-id-wrapper={`${randNum}-${sharedProduct.code}`}>
       { !isBA && !isBoutiqueApp && (<WishListIcon pCode={sharedProduct.code} pName={sharedProduct.name} isWishlist={isInWishList} index={randNum} isChat={props.msgTime ? true : false}/>)}
        <div className="product-item__content-details" data-id={`${randNum}-${sharedProduct.code}`}>
          <div role='heading' aria-level={ariaLevelSharedProductHeading} className='modal__heading' id={uniqueId}>
            <span role='presentation' className='prod-item__title heading'>
              {sharedProduct.name}
            </span>
            {sharedProduct.selectedVariantData && (
              <span role='presentation' className='prod-item__shade'>
                {sharedProduct.selectedVariantData.shadeAsset && sharedProduct.selectedVariantData.shadeAsset.url && <img src={sharedProduct.selectedVariantData.shadeAsset.url} alt=''></img>}
                {sharedProduct.selectedVariantData.shade && <span className='shade__name'>{sharedProduct.selectedVariantData.shade}</span>}
              </span>
            )}
            {sharedProduct.shade && (
              <span role='presentation' className='prod-item__shade'>
                {sharedProduct.shade.url && <img src={sharedProduct.shade.url} alt=''></img>}
                {sharedProduct.shade.shade && <span className='shade__name'>{sharedProduct.shade.shade}</span>}
              </span>
            )}
            <span className='is-sr-only'>{`${productRef} ${sharedProduct.code}`}</span>
          </div>
          <p className="price__text">{priceValue}</p>
          {props.isShareProductMessage && <p className="is-sr-only">{availableAct}</p>}

          {isBA && <p className="share__text"> {shareProductText} </p>}
          {!isBA && !isBoutiqueApp && <Button
            buttonText={<span>{addToCartCTA}</span>}
            onClick={() => addToBag(randNum, sharedProduct, ariaLabelMainHeading)}
            buttonClass={`is-tertiary ${props.msgTime ? 'chat-btn' : ''}`}
            ariaDescribedby={uniqueId} />
          }
        </div>
      </div>
      }
    </Fragment>
  )
}

export default ShareProductModalContent