import { useEffect, useContext } from 'react'
import { VideoContext } from '../Components/chanelTwilioVideo'
import { a11yNotificationText, removeParticipantAttrtibutes } from '../Utils/helper'
 
export default function useClients() {
  
  const { conversation, setNotificationModalIsOpen, clientList, setClientList, setUserIconText } = useContext(VideoContext)
  const { isMasterClass } = jsConfigSecond.twilio


  useEffect(() => {
    if (conversation) {

      const participantJoined = async (participant) => {
        if (participant.identity.split('$')[2] === 'client'){
        console.log(participant.identity + 'joined ')
        var convUser = await participant.getUser();
        convUser.on('updated', (data)=>{
          if(data.updateReasons.includes("reachabilityOnline")){
            console.log(data.user.identity +' : ' + data.user.isOnline);
            addParticipantToClientList (data.user,participant)
          }
          // data.unsubscribe();
        })
        addParticipantToClientList (convUser,participant)
      }
    }
      const addParticipantToClientList = (convUser,participant) => {
        if (convUser.isOnline) {
          showClientNotification(participant)
        }
        else{
          removeParticipantAttrtibutes(participant)
          participantLeft(participant)
        }
      }

      const showClientNotification = (participant) => {
        setClientList(prevParticipants => [...prevParticipants, participant])
        setUserIconText(a11yNotificationText(clientList))
        setNotificationModalIsOpen(true)
      }

      const participantLeft = (participant) => {
        console.log(participant.identity + 'left')
        if (participant.identity.split('$')[2] === 'client') {
          setClientList(prevParticipants => prevParticipants.filter(p => p.identity !== participant.identity))
          !isMasterClass &&  setNotificationModalIsOpen(false)
        }
      }
        conversation.participants.forEach(participantJoined)
        conversation.on('participantJoined', participantJoined)
        conversation.on('participantLeft', participantLeft)

      return () => {
        conversation.off('participantJoined', participantJoined)
        conversation.off('participantLeft', participantLeft)
      }
    }
  }, [])

}