const ocAssetPath = `${config.theme}/patterns/scripts/ui/twilio/oc-assets`
import { select } from 'lib/selectors'

let images = {}
let virtualBackground

const loadImage = (name) =>
  new Promise((resolve) => {
    const image = new Image();
    image.src = `${ocAssetPath}/${name}`
    image.sizes = 'auto'
    image.onload = () => resolve(image);
  })

 if (select('.twilio-content-container')) {
   Promise.all([
     loadImage('oc-background-branded.png')
   ]).then(([branded]) => {
   images.branded = branded
   return images
  })
 }

const loadBackground = async () => {
  import('@twilio/video-processors')
    .then(async (videoProcessor) => {
      if (!virtualBackground) {
        virtualBackground = new videoProcessor.VirtualBackgroundProcessor({
          assetsPath: ocAssetPath,
          backgroundImage: images.branded,
          maskBlurRadius: 5,
          fitType: 'cover'
        })
        await virtualBackground.loadModel()
      }
    })
  }

const addBackground = async (track) => {
  setProcessor(virtualBackground, track)
}

const removeBackground = async (track) => {
  setProcessor(null, track)
}

const setProcessor = (processor, track) => {
  if (track.processor) {
    track.removeProcessor(track.processor);
  }
  if (processor) {
    track.addProcessor(processor);
  }
}


export { addBackground, removeBackground, loadBackground }