import React, { useEffect, useState, useContext } from 'react'
import { select } from 'lib/selectors'
import CatalogListElement from './catalogListElement'
import { VideoContext } from './chanelTwilioVideo'
import * as xhr from '../../../modules/fetch'
import ConfirmationModal from './confirmationModal'
import { focusElement } from '../../../lib/helpers'
import Loader from 'ui/widgets/loader'


const MyList = (props) =>
{
  const { a11y, handleSharedProduct, updateMyListProductCount, myListProductCount } = props
  const { myListTabText, removeProductFromList } = jsConfigSecond.twilio
  const [isListTab, setisListTab] = useState(false)
  const { room, addedProductList, setAddedProductList } = useContext(VideoContext)
  const [showRemoveProdModel,setShowRemoveProdModel] = useState(false)
  const [prodToDel, setProdToDel] = useState(null)

  const onShareProductClicked = (product) =>
  {
    handleSharedProduct(product)
  }

  const deleteProductClicked = (delProduct) => {
    setProdToDel(delProduct)
    setShowRemoveProdModel(true)
  }

    
  const closeRemoveProdModel = () => {setShowRemoveProdModel(false)}
  
  const handleDeleteProduct = () => {
    const button = select('.confirm_prod')
    const loader = new Loader(button, {
      button: true
    })
    loader.init()
    loader.create()
  	const requestdata = {
        'room-id': room.name,
        productCode: prodToDel.code,
    }
	xhr.get(removeProductFromList, { data: requestdata })
    .then((res) =>
    {
      setAddedProductList(Array.from(res.products))
      updateMyListProductCount()
      if (loader) {
        loader.destroy()
      }
      closeRemoveProdModel()
      focusElement(select('.js-level2-heading'))
      console.log('Product deleted from database')
    })
    .catch((err) =>
    {
      console.error(err)
    })
  }
 
  useEffect(() =>
  {
    setisListTab(true)
    a11y ? select('.js-level2-heading').focus() : ''
    const btnMyListTab = select('button.my-list-tab')
    btnMyListTab && btnMyListTab.setAttribute('aria-current', true)

    return () =>
    {
      btnMyListTab.removeAttribute('aria-current')
    }
  }, [])

  return (
    <div className='catalog-search'>
      <div className='my-list__result__container'>
        <div className='prod__list'>
          <div role='heading' aria-level='2' className='is-sr-only js-level2-heading' tabIndex='-1'>{`${myListTabText} (${myListProductCount})`}</div>
          {addedProductList && addedProductList.map((item, index) => <CatalogListElement isListTab={isListTab} key={index} item={item} handleSharedProduct={onShareProductClicked} deleteProductClicked={deleteProductClicked} />)}
          {showRemoveProdModel && <ConfirmationModal item={prodToDel} handleDeleteProduct={handleDeleteProduct} showRemoveProdModel={showRemoveProdModel} closeRemoveProdModel={closeRemoveProdModel}/>}
        </div>
      </div>
    </div>
  )
}

export default MyList
