import React, { useEffect } from 'react'
import Button from "./button"
import { select } from 'lib/selectors'

const WishListIcon = (props) => {
	
   const {pCode, pName, isWishlist, index, isChat} = props
   const { addToWishlist, removeFromWishlist } = jsConfigSecond.twilio
   var hiddenTxt = addToWishlist
   const uniqueId = isChat ? `product-list-chat-${index}-${pCode}`: `product-list-popin-${index}-${pCode}`

   const wishlistData = {
     "eventAction": pName,
     "eventLabel": pCode
   }

   if (isWishlist) {
     hiddenTxt = removeFromWishlist
   }

   return (
      <Button buttonClass={`is-icon is-wishlist wishlist-icon ` + (isWishlist ? ' is-active' : '')} type="button" dataTrackWishlist={JSON.stringify(wishlistData)} dataWishlist={pCode} ariaDescribedby={uniqueId}
       dataTextActive={addToWishlist} dataTextInactive={removeFromWishlist} buttonText={<span>{hiddenTxt}</span>} iconName="wishlist" spanClass="is-sr-only" dataTextWishlistActive={addToWishlist} dataTextWishlistInactive={removeFromWishlist}/>
   )
 }

export default WishListIcon