import React, { useEffect, useState, useContext } from 'react'
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs'
import { select } from 'lib/selectors'
import CloseButton from './closeButton'
import Button from './button'
import MyList from './myList'
import CatalogSearch from './catalogSearch'
import { isMobileScreen } from 'lib/helpers'
import UserIcon from './userIcon'
import * as xhr from '../../../modules/fetch'
import { functionalCTADLPush } from '../Utils/helper'
import { ShareProductContext } from './chanelTwilioConsultation'
import { VideoContext } from './chanelTwilioVideo'


const ProductCatalog = (props) =>
{
    const { closeText, searchMainText, searchTabText, myListTabText, isBA, saveSharedProduct } = jsConfigSecond.twilio
    const [tabIndex, setTabIndex] = useState(0)
    const [isMobileView, setIsMobileView] = useState(isMobileScreen())
    const [a11y, setA11y] = useState(false)
    const [myListProductCount, setMyListProductCount] = useState(0)
    const [loadSearch, setLoadSearch] = useState(false)
    const { setShareProduct, setIsShareProductModalOpen, setIsProdCatEnabled } = useContext(ShareProductContext)
    const { room, conversation, addedProductList, setAddedProductList } = useContext(VideoContext)

    const resizeHandler = () =>
    {
        setIsMobileView(isMobileScreen())
    }

    const updateMyListProductCount = () =>
    {
        setMyListProductCount(myListProductCount - 1)
    }

    const onBackClicked = () =>
    {
        props.closeClicked(false)
    }

    const handleScroll = () =>
    {
        props.closeNotificationModal()
    }

    const onTabClick = (tab1, tab2) =>
    {
        const selectedTabPanel = select(tab1)
        const unSelectedTabPanel = select(tab2)
        selectedTabPanel ? selectedTabPanel.removeAttribute('hidden') : ''
        unSelectedTabPanel ? unSelectedTabPanel.setAttribute('hidden', true) : ''
        setA11y(true)
    }

    const onAddToListClicked = (product) =>
    {
        console.log('Product added')
        setAddedProductList((addedProductList) => [...addedProductList, product])

        const requestdata = {
            'room-id': jsConfigSecond.twilio.roomId,
            productCode: product.code,
        }
        xhr.get(jsConfigSecond.twilio.addProductToList, { data: requestdata })
            .then((res) =>
            {
                console.log('Product added in database')
                setMyListProductCount(myListProductCount + 1)
            })
            .catch((err) =>
            {
                console.error(err)
            })
        functionalCTADLPush('add_to_list', 'popin', product.name)
    }

    const handleSharedProduct = (sharedProduct) =>
    {
        functionalCTADLPush('share_product', 'list_page', sharedProduct.name)

        const requestdata = {
            'room-id': room.name,
            productCode: sharedProduct.code,
        }
        xhr.get(saveSharedProduct, { data: requestdata })
            .then((res) =>
            {
                setIsProdCatEnabled(false)
                select('.consultation__container').classList.remove('is-hidden')
                setShareProduct(sharedProduct)
                setIsShareProductModalOpen(true)
                sendProductToClient(sharedProduct)
                console.log('Shared product added in database')

            })
            .catch((err) =>
            {
                console.error(err)
            })
    }

    const sendProductToClient = (sharedProduct) =>
    {
        const productObjToShare = removeEmptyOrNull(sharedProduct)
        conversation.sendMessage(JSON.stringify(productObjToShare), { "type": "sharedProduct", "isBA": isBA })
        console.log("Product shared to Client");
    }

    const removeEmptyOrNull = (obj) =>  {
        obj["baseOptions"] && delete obj["baseOptions"]
        Object.entries(obj).forEach(([k, v]) => {
            if (v && typeof v === 'object') {
                removeEmptyOrNull(v)
            }
            if (v && typeof v === 'object' && !Object.keys(v).length || v == null ) {
                delete obj[k]
            }
        })
        return obj
    }

    useEffect(() =>
    {
        select('body').classList.remove('has-modal')

        const myListPanel = select('#react-tabs-3')
        myListPanel ? myListPanel.setAttribute('hidden', true) : ''
        const catalogContainer = select('.catalog__container')
        const notIcon = select('.notification-icon', catalogContainer)
        notIcon ? notIcon.setAttribute('aria-live', 'assertive') : ''

        window.addEventListener('resize', resizeHandler)
        window.addEventListener('scroll', handleScroll)

        if (isBA) {
            const requestdata = {
                'room-id': room.name,
            }
            xhr.get(jsConfigSecond.twilio.fetchOCProductListForBA, { data: requestdata })
                .then((res) =>
                {
                    setAddedProductList(Array.from(res.products))
                    setMyListProductCount(res.products.length)
                    setLoadSearch(true)
                })
                .catch((err) =>
                {
                    console.error(err)
                    setLoadSearch(true)
                })
        }


        return () =>
        {
            select('body').classList.add('has-modal')
            window.removeEventListener('resize', resizeHandler)
            window.removeEventListener('scroll', handleScroll)
        }
    }, [])

    // all consts variables
    const tabText = myListProductCount > 0 ? `(${myListProductCount})` : ``

    return (
        <div className='catalog__container' role='dialog' aria-label={searchMainText} aria-modal='true'>
            <div role='document'>
                <div className='catalog__header'>
                    <div className='catalog__header-left'>
                        <div className='icon-quit'>
                            <CloseButton iconName='back' onClick={onBackClicked} buttonText={`${closeText} - ${searchMainText}`} />
                        </div>
                        <p role='heading' aria-level='1' className='is-sr-only'>
                            {searchMainText}
                        </p>
                    </div>
                    <div className='catalog__header-right'>
                        <div className='user-icon'>
                            <UserIcon
                                onAcceptClicked={props.onAcceptClicked}
                                closeNotificationModal={props.closeNotificationModal}
                                openNotificationModal={props.openNotificationModal}
                            />
                        </div>
                    </div>
                </div>
                <div className='catalog__body'>
                    <Tabs selectedIndex={tabIndex} onSelect={(index) => setTabIndex(index)}>
                        <TabList className='tab-list'>
                            <Tab tabIndex='-1'>
                                <Button onClick={() => onTabClick('#react-tabs-1', '#react-tabs-3')} buttonClass='search-tab' buttonText={searchTabText} />
                            </Tab>

                            <Tab tabIndex='-1'>
                                <Button onClick={() => onTabClick('#react-tabs-3', '#react-tabs-1')} buttonClass='my-list-tab' buttonText={`${myListTabText} ${tabText}`} />
                            </Tab>
                        </TabList>

                        <TabPanel>
                            {loadSearch && <CatalogSearch isMobileView={isMobileView} a11y={a11y} onAddToListClicked={onAddToListClicked} handleSharedProduct={handleSharedProduct} />}
                        </TabPanel>

                        <TabPanel>
                            <MyList a11y={a11y} handleSharedProduct={handleSharedProduct} totalParticipant={props.totalParticipant} updateMyListProductCount={updateMyListProductCount} myListProductCount={myListProductCount}/>
                        </TabPanel>
                    </Tabs>
                </div>
            </div>
        </div>
    )
}

export default ProductCatalog
